import { IPropertyPaneButtonProps } from './IPropertyPaneButton';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Button on the PropertyPane.
 * @param targetProperty - Target property the Button is associated to.
 * @param properties - Strongly typed Button properties.
 */
export function PropertyPaneButton(
  targetProperty: string,
  properties: IPropertyPaneButtonProps): IPropertyPaneField<IPropertyPaneButtonProps> {

  return {
    type: IPropertyPaneFieldType.Button,
    targetProperty: targetProperty,
    properties: properties
  };
}



