abstract class GuidHelpers {
  public static isValid(guid: string): boolean {
    const regex: RegExp = new RegExp('^[a-f0-9]{8}(?:-[a-f0-9]{4}){3}-[a-f0-9]{12}$', 'i');
    return regex.test(guid);
  }

  public static requireValid(guid: string): void {
    if (!GuidHelpers.isValid(guid)) {
      throw Error(`Invalid GUID syntax ${guid}`);
    }
  }

  /**
   * Example: "/Guid(d5369f3b-bd7a-412a-9c0f-7f0650bb5489)/"
   *          -> 'd5369f3b-bd7a-412a-9c0f-7f0650bb5489'
   * Example: "{d5369f3b-bd7a-412a-9c0f-7f0650bb5489}"
   *          -> 'd5369f3b-bd7a-412a-9c0f-7f0650bb5489'
   */
  public static getNormalized(guid: string): string {
    let normalized: string = guid;

    const regexp: RegExp = new RegExp('^/Guid\\((.*)\\)/', 'i');
    const result: string[] = regexp.exec(guid);
    if (result) {
      normalized = result[1];
    }

    if (normalized.length === 38) {
      // Strip leading/trailing {}
      normalized = normalized.substr(1, 36);
    }

    GuidHelpers.requireValid(normalized);
    return normalized.toLowerCase();
  }

  /* tslint:disable:no-bitwise */
  public static generateGuid(): string {
    return 'AAAAAAAA-AAAA-4AAA-BAAA-AAAAAAAAAAAA'
      .replace(/[AB]/g, (character: string): string => {
        const num: number = Math.random() * 16 | 0;
        const masked: number = character === 'A' ? num : (num & 0x3 | 0x8);
        return masked.toString(16);
      });
  }

}

export default GuidHelpers;



