import { IPropertyPaneLinkProps } from './IPropertyPaneLink';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Link on the PropertyPane.
 * @param targetProperty - Target property the Link is associated to.
 * @param properties - Strongly typed Link properties.
 */
export function PropertyPaneLink(
  targetProperty: string,
  properties: IPropertyPaneLinkProps): IPropertyPaneField<IPropertyPaneLinkProps> {

  return {
    type: IPropertyPaneFieldType.Link,
    targetProperty: undefined,
    properties: properties
  };
}


