import { IRichTextEditorLoader } from '../rte/IRichTextEditorLoader';
import QuillInstance = require('QuillInstance');
import { _TraceLogger, _LogSource } from '@microsoft/sp-client-base';

export default class QuillLoader implements IRichTextEditorLoader {
  private static logSource: _LogSource = _LogSource.create('SPRteQuillLoader');
  private static _quillPromise: Promise<QuillInstance>;
  private static _quillModule: QuillInstance;

  public loadModule(): Promise<QuillInstance> {
    if (!QuillLoader._quillPromise) {
      QuillLoader._quillPromise = new Promise<QuillInstance>(
        (complete?: (result: QuillInstance) => void) => {
          require.ensure(
            ['quill'],
            (require: <T, dummy>(path: string) => T) => {
              const quill: QuillInstance = require('quill') as QuillInstance;
              QuillLoader._quillModule = quill;
              complete(quill);
            },
            'sp-client-preview-quill');
        }
      );

      QuillLoader._quillPromise.catch((error: Error) => {
        _TraceLogger.logError(QuillLoader.logSource, error);
      });
    }

    return QuillLoader._quillPromise;
  }
}



