import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Horizontal Rule on the PropertyPane.
 * @param properties - Strongly typed Horizontal Rule properties.
 */
export function PropertyPaneHorizontalRule(): IPropertyPaneField<void> {
  return {
    type: IPropertyPaneFieldType.HorizontalRule,
    targetProperty: undefined,
    properties: undefined
  };
}



