import {
  SPFlight,
  SPKillSwitch,
  IClientSideComponentManifest,
  IClientSideApplicationManifest,
  ServiceScope,
  IDigestCache,
  digestCacheServiceKey,
  SPCdnUrlProvider,
  IODataWeb,
  IODataNavigationNode,
  IODataNavigationNodeCollection,
  IODataContextWebInformation
} from '@microsoft/sp-client-base';

import IOdataList from '../httpClient/odataTypes/IOdataList';
import IOdataListItem from '../httpClient/odataTypes/IOdataListItem';
import IOdataUser from '../httpClient/odataTypes/IOdataUser';
import { ISPPageContextInfo } from '../httpClient/odataTypes/ISPPageContextInfo';
import GuidHelpers from '../basicTypes/GuidHelpers';
import PageContext from '../pageContext/AppPageContext';
import SPNavigationNode from '../pageContext/SPNavigationNode';
import SPNavigationNodeCollection from '../pageContext/SPNavigationNodeCollection';

/**
  * This is a global variable that is embedded on pages rendered by the classic
  * server-side rendering model.  We are relying on it only as a temporary workaround
  * until we've completed the work to host a first party client-side application
  * which will provide the expected preloadedData structure.
  * (SPPPlat VSO task #147515)
  */
declare var _spPageContextInfo: {
  siteAbsoluteUrl: string;
  webAbsoluteUrl: string;        // ex. 'http://example.com/sites/PubSite'
  webTitle: string;
  webServerRelativeUrl: string;  // ex. '/sites/PubSite'
  siteServerRelativeUrl: string;
  serverRequestPath: string;     // ex. '/sites/PubSite/Pages/default.aspx'
  pageItemId: number;            // ex. 2
  pageListId: string;            // ex. "{9fb9199b-65f2-4a4a-b597-11d1a44422c1}"
  isSiteAdmin: boolean;

  webLanguage: number;           // ex. 1033
  currentCultureName: string;    // ex. "en-US"
  currentLanguage: number;       // ex. 1033
  currentUICultureName: string;  // ex. "en-US"

  navigationInfo: IODataNavigationNodeCollection;
};

export default class PreloadedDataReader {
  private _preloadedData: IPreloadedData;
  private _isValid: boolean;

  private static _toNavNodes(
    srcNodes: IODataNavigationNode[],
    pageContext: PageContext): SPNavigationNode[] {

    const items: SPNavigationNode[] = new Array<SPNavigationNode>();

    if (srcNodes) {
      for (let i: number = 0; i < srcNodes.length; i++) {
        const srcItem: IODataNavigationNode = srcNodes[i];
        const item: SPNavigationNode = new SPNavigationNode(pageContext);
        item.load({ id: srcItem.Id });
        item.updateOdataObject(srcItem);

        items.push(item);
      }
    }

    return items;
  }

  constructor(preloadedData: IPreloadedData) {
    this._preloadedData = preloadedData;
    this._isValid = false;

    if (this._preloadedData === undefined ||
      !GuidHelpers.isValid(this._preloadedData.clientSideApplicationId)) {
      console.log('Warning: PreloadedDataReader: No preloaded data was found');
    } else {
      this._isValid = true;

      if (this._preloadedData.spPageContextInfo) {
        SPFlight.initialize(this._preloadedData.spPageContextInfo.ExpFeatures);
        SPKillSwitch.initialize(this._preloadedData.spPageContextInfo.killSwitches);
      }
    }
  }

  public initializeDigestCache(serviceScope: ServiceScope): void {
    if (this._isValid) {
      // @todo: (VSO task #163923) This will need to be updated when PreloadedDataReader
      // is wired up to the final version of HttpClient from sp-client-base.
      if (this._preloadedData.contextWebInfo
        && this._preloadedData.spPageContextInfo
        && this._preloadedData.spPageContextInfo.webServerRelativeUrl) {

        serviceScope.whenFinished(() => {
          const digestCache: IDigestCache = serviceScope.consume(digestCacheServiceKey);
          console.log('PreloadedDataReader: Added preloaded FormDigestValue to cache');

          const expirationTimestamp: number = performance.now()
            + 1000 * this._preloadedData.contextWebInfo.FormDigestTimeoutSeconds;

          digestCache.addDigestToCache(
            this._preloadedData.spPageContextInfo.webServerRelativeUrl,
            this._preloadedData.contextWebInfo.FormDigestValue,
            expirationTimestamp
          );
        });
      }
    }
  }

  public initializePageContext(pageContext: PageContext): void {
    if (this._isValid) {
      pageContext.application.load({
        clientSideApplicationId: this._preloadedData.clientSideApplicationId,
        hostedByClassicPage: false
      });

      pageContext.web.updateOdataObject(undefined);
      pageContext.list.updateOdataObject(undefined);
      pageContext.listItem.updateOdataObject(undefined);
      pageContext.core.updateOdataObject(undefined);

      const web: IODataWeb = this._preloadedData.web;
      if (web) {
        pageContext.web.load({
          id: GuidHelpers.getNormalized(web.Id),
          serverRelativeUrl: web.ServerRelativeUrl,
          title: web.Title
        });
        pageContext.web.updateOdataObject(web);
      }

      const list: IOdataList = this._preloadedData.list;
      if (list) {
        pageContext.list.load({
          id: GuidHelpers.getNormalized(list.Id),
          title: list.Title,
          description: list.Description
        });
        pageContext.list.updateOdataObject(list);
      }

      const listItem: IOdataListItem = this._preloadedData.item;
      if (listItem) {
        pageContext.listItem.load({
          id: listItem.ID
        });
        pageContext.listItem.updateOdataObject(listItem);
      }

      const user: IOdataUser = this._preloadedData.user;
      if (user) {
        pageContext.user.load({
          id: user.Id
        });
        pageContext.user.updateOdataObject(user);
      }

      const spPageContextInfo: ISPPageContextInfo = this._preloadedData.spPageContextInfo;
      if (spPageContextInfo) {
        pageContext.core.load({
          MenuData: spPageContextInfo.MenuData,
          currentCultureName: spPageContextInfo.currentCultureName,
          currentUICultureName: spPageContextInfo.currentUICultureName,
          correlationId: spPageContextInfo.CorrelationId,
          spoEnvironment: spPageContextInfo.env,
          farmLabel: spPageContextInfo.farmLabel,
          groupColor: spPageContextInfo.groupColor,
          hasManageWebPermission: spPageContextInfo.hasManageWebPermission,
          layoutsUrl: spPageContextInfo.layoutsUrl,
          listPermsMask: spPageContextInfo.listPermsMask,
          siteAbsoluteUrl: spPageContextInfo.siteAbsoluteUrl,
          siteClientTag: spPageContextInfo.siteClientTag,
          siteId: spPageContextInfo.siteId,
          siteServerRelativeUrl: spPageContextInfo.siteServerRelativeUrl,
          siteSubscriptionId: spPageContextInfo.siteSubscriptionId,
          systemUserKey: spPageContextInfo.systemUserKey,
          themedCssFolderUrl: spPageContextInfo.themedCssFolderUrl,
          userDisplayName: spPageContextInfo.userDisplayName,
          userLoginName: spPageContextInfo.userLoginName,
          webAbsoluteUrl: spPageContextInfo.webAbsoluteUrl,
          webId: spPageContextInfo.webId,
          webLogoUrl: spPageContextInfo.webLogoUrl,
          webServerRelativeUrl: spPageContextInfo.webServerRelativeUrl,
          webTitle: spPageContextInfo.webTitle,
          webTemplate: spPageContextInfo.webTemplate,
          webPermMasks: spPageContextInfo.webPermMasks
        });
        pageContext.core.updateOdataObject(spPageContextInfo);
      }

      if (this._preloadedData.publicCdnOrigin) {
        SPCdnUrlProvider.initialize(this._preloadedData.publicCdnOrigin);
      }

      // NOTE: We have to access the window using this syntax.  Originally we
      // used the conventional approach, but it caused a compatibility problem
      // for the IE web browser.
      /* tslint:disable:no-string-literal */
    } else if (window['_spPageContextInfo'] !== undefined) {
      // Fallback for classic pages
      pageContext.application.load({
        clientSideApplicationId: '00000000-0000-0000-0000-000000000000',
        hostedByClassicPage: true
      });

      pageContext.web.load({
        id: '00000000-0000-0000-0000-000000000000',
        serverRelativeUrl: _spPageContextInfo.webServerRelativeUrl,
        title: _spPageContextInfo.webTitle
      });

      if (_spPageContextInfo.pageListId) {
        pageContext.list.load({
          id: GuidHelpers.getNormalized(_spPageContextInfo.pageListId),
          title: '',
          description: ''
        });

        if (_spPageContextInfo.pageItemId) {
          pageContext.listItem.load({
            id: _spPageContextInfo.pageItemId
          });
        }
      }
    } else {
      // @todo: This breaks unit tests.
      // throw new Error('The _spClientSidePageContext variable is missing');
    }

    if (this._preloadedData.spPageContextInfo && this._preloadedData.spPageContextInfo.navigationInfo) {
      this._initNav(
        pageContext.quickLaunch,
        this._preloadedData.spPageContextInfo.navigationInfo.quickLaunch,
        pageContext);
      this._initNav(
        pageContext.topNav,
        this._preloadedData.spPageContextInfo.navigationInfo.topNav,
        pageContext);
    }
  }

  private _initNav(
    target: SPNavigationNodeCollection,
    source: IODataNavigationNode[],
    pageContext: PageContext): void {
    if (!target || !source) {
      return;
    }

    target.updateOdataObject(source);

    const items: SPNavigationNode[] = PreloadedDataReader._toNavNodes(source, pageContext);
    target.items = items;
  }
}

export interface IPreloadedData {
  clientSideApplicationId: string;
  contextWebInfo?: IODataContextWebInformation;
  spPageContextInfo?: ISPPageContextInfo;
  user?: IOdataUser;
  web?: IODataWeb;
  list?: IOdataList;
  item?: IOdataListItem;
  page?: { DoesUserHaveEditPermission: boolean };
  quickLaunch?: IODataNavigationNodeCollection;
  manifests?: IClientSideComponentManifest[];
  publicCdnOrigin?: string[];

  // Only in when the application uses manifestVersion=2:
  clientSideApplicationManifest?: IClientSideApplicationManifest;
}


