import * as React from 'react';
import { DisplayMode } from '@microsoft/sp-client-base';
import { IClientSideWebPartManifest } from '@microsoft/sp-module-interfaces';

import ClientSideWebPartManager from '../../../../clientSideWebParts/core/ClientSideWebPartManager';
import { ICanvasContext } from '../../sp-canvas/canvas/canvasComponent/ICanvasComponent';
import ISerializedCanvasControl from '../canvas/ISerializedCanvasControl';
import IWebPartData from '../../../../clientSideWebParts/core/IWebPartData';
import IWebPartManagerContext from '../../../../clientSideWebParts/core/IWebPartManagerContext';
import { PropertyPaneState } from '../../../../common/webParts/PropertyPaneDefinitions';
import { getFirstFocusableChild } from '../../sp-canvas/a11y/CanvasA11y';

export interface IWebPartZoneState {
  displayMode: DisplayMode;
}

export interface IWebPartZoneProps extends ISerializedCanvasControl {
  webPartData: IWebPartData;
  webPartId: string;
  /* tslint:disable:no-any */
  webPartManifest: IClientSideWebPartManifest<any>;
  /* tslint:enable:no-any */
}

export default class WebPartZone extends React.Component<IWebPartZoneProps, IWebPartZoneState> {
  public static contextTypes: React.ValidationMap<WebPartZone> = {
    store: React.PropTypes.object.isRequired
  };
  public static webPartManagerInitialized: boolean = false;

  public context: ICanvasContext;

  /**
   * Indicates whether the web part has loaded and is safe to perform synchronous actions.
   */
  private _isLoaded: boolean;

  /**
   * The promise is used to perform actions after the async loading of the web part code. The web
   * part manager only creates a reference in its map after the web part code is loaded. Therefore,
   * invoking methods on the web part manager that reference an unloaded web part is invalid.
   */
  private _loadWebPartPromise: Promise<void>;

  private _webPartDiv: HTMLElement;
  private _webPartManager: ClientSideWebPartManager;

  constructor(props: IWebPartZoneProps, context: ICanvasContext) {
    super(props, context);
    this._webPartManager = this.context.store.webPartManager;

    this.handleConfigureButtonClicked = this.handleConfigureButtonClicked.bind(this);
    this.state = {
      displayMode: this.props.displayMode
    };
  }

  // @todo This is a workaround for deserialization of a Canvas without the manifests.
  // We need to think about a longer term solution.
  public componentDidMount(): void {
    const context: IWebPartManagerContext = {
      domElement: this._webPartDiv,
      instanceId: this.props.id,
      manifest: this.props.webPartManifest,
      displayMode: this.props.displayMode,
      webPartData: this.props.webPartData
    };

    this._loadWebPartPromise = this._webPartManager.loadWebPart(context).then(() => {
      if (this.state.displayMode === DisplayMode.Edit && this.props.id === this.context.store.selectedControlId) {
        this.context.store.webPartManager.configureStart(this.props.id);
      }

      // @todo: Currently loadWebPart() swallows exceptions
      this._isLoaded = true;
    });
  }

  public shouldComponentUpdate(newProps: IWebPartZoneProps, newState: IWebPartZoneState): boolean {
    // Defer all updating to the web part manager and the web part
    return false;
  }

  public render(): React.ReactElement<IWebPartZoneProps> {
    return (
      <div
        ref={ (htmlElement: HTMLElement) => this._webPartDiv = htmlElement }
        style= { { width: '100%' } } >
      </div>
    );
  }

  public get instanceId(): string {
    return this.props.id;
  }

  public get isFocusable(): boolean {
    return !!getFirstFocusableChild(this._webPartDiv);
  }

  public focus(): void {
    if (!this.context.store.a11yManager.navigateInside(this._webPartDiv)) {
      this.context.store.a11yManager.navigateOutOf(this._webPartDiv);
    }
  }

  public handleConfigureButtonClicked(shouldToggle: boolean): void {
    /**
     * Using _isLoaded flag instead of the promise because we want a click on the configure button to
     * no-op. If we had used a promise, the web part selection could have shifted by the time the
     * loading is complete and thus incorrectly change the context of the property pane. A no-op makes
     * sense because the web part would have no UI rendered yet, and the framework indicates the web part
     * is still in the process of loading.
     */
    if (this._isLoaded) {
      this._webPartManager.configureStart(
        this.props.id,
        shouldToggle ? PropertyPaneState.Toggle : PropertyPaneState.Open
      );
    }
  }

  public handleDeleteButtonClicked(): void {
    /**
     * If a web part was still in the process of loading and then the zone is deleted from the Canvas,
     * we still want to ensure the web part manager cleans up its references and disposes the web part.
     */
    this._loadWebPartPromise.then(() => this._webPartManager.onWebPartDelete(this.props.id));
  }

  public serialize(): ISerializedCanvasControl {
    const tempArray: string[] = [this.props.id];

    /**
     * If the web part has not loaded, we can safely return the web part data we are trying to load with because
     * the data could not have been modified.
     */
    const webPartData: IWebPartData =
      this._isLoaded ? this._webPartManager.serialize(tempArray).get(this.props.id) : this.props.webPartData;
    const serialization: IWebPartZoneProps = JSON.parse(JSON.stringify(this.props));

    serialization.webPartManifest = undefined;
    serialization.webPartData = webPartData;

    return serialization;
  }

  public setDisplayMode(newDisplayMode: DisplayMode): void {
    this.setState({
      displayMode: newDisplayMode
    });

    /**
     * The Canvas zone will switch mode, and then wait for the web part to fully render before asking
     * the web part manager to switch the mode of the web part.
     */
    this._loadWebPartPromise.then(() => this._webPartManager.setDisplayMode(newDisplayMode, this.props.id));
  }
}



