import { IPropertyPaneCustomFieldProps } from './IPropertyPaneCustomField';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Choice Group on the PropertyPane.
 * @param targetProperty - Target property the choice group is associated to.
 * @param properties - Strongly typed Choice Group properties.
 */
export function PropertyPaneCustomField(
  targetProperty: string,
  properties: IPropertyPaneCustomFieldProps): IPropertyPaneField<IPropertyPaneCustomFieldProps> {
  return {
    type: IPropertyPaneFieldType.Custom,
    targetProperty: undefined,
    properties: properties
  };
}



