import PageContext from './AppPageContext';
import PageContextItem, { PageContextItemState } from './PageContextItem';
import GuidHelpers from '../basicTypes/GuidHelpers';

/**
  * This interface is used with ApplicationContext.load()
  */
export interface IApplicationContextArgs {
  clientSideApplicationId: string;
  hostedByClassicPage: boolean;
}

/**
 * Tracks contextual information about the client-side application that
 * is currently running.
 */
export default class ApplicationContext extends PageContextItem {
  private _clientSideApplicationId: string;
  private _hostedByClassicPage: boolean;

  constructor(pageContext: PageContext) {
    super(pageContext);

    this._clientSideApplicationId = undefined;
    this._hostedByClassicPage = undefined;
  }

  /**
   * Assigns all properties of the object, then assigns the "state" property
   * to PageContextItemState.Ready.
   */
  public load(args: IApplicationContextArgs): void {
    this.validateLoad();

    GuidHelpers.requireValid(args.clientSideApplicationId);
    this._clientSideApplicationId = args.clientSideApplicationId;
    this._hostedByClassicPage = args.hostedByClassicPage;
    this.state = PageContextItemState.Ready;
  }

  /**
   * Returns the client side component identifier for the client-side application.
   * This corresponds to the "id" field in the application's manifest JSON file.
   * Example: "00000000-0000-0000-0000-000000000000"
   */
  public get clientSideApplicationId(): string {
    return this._clientSideApplicationId;
  }

  /**
   * Indicates whether the application is currently being hosted by a classic page,
   * e.g. if a client side web part has been added to a classic publishing page.
   */
  public get hostedByClassicPage(): boolean {
    return this._hostedByClassicPage;
  }
}



