import { IClientSideWebPartManifest } from '@microsoft/sp-module-interfaces';
import { DisplayMode, _TraceLogger, _LogSource } from '@microsoft/sp-client-base';

import ClassicPageHost from './ClassicPageHost';
import ClientSideWebPartManager from '../ClientSideWebPartManager';
import IWebPartData from '../IWebPartData';
import IWebPartManagerContext from '../IWebPartManagerContext';
import { PropertyPaneState } from '../../../common/webParts/PropertyPaneDefinitions';

/**
 * This class helps bootstrap client side web parts onto classic SharePoint pages.
 * Ideally client side web parts should be bootstrapped similar to the modern pages.
 * @todo (SPPPLAT VSO#228547) tracks fixing that.
 *
 * @internal
 */
export default class ClassicPageBootstrapper {

    private _initialized: boolean = false;
    private _manager: ClientSideWebPartManager = undefined;
    private _logSource: _LogSource = _LogSource.create('ClientSideWebPart:ClassicPageBootstrapper');

    /**
     * Load client side web part on a classic page
     *
     * @param instanceId - Instance id of the webpart.
     * @param manifest - The webp art manifest.
     * @param data - Instance data for the control.
     * @param mode - DisplayMode for the web part.
     */
    /* tslint:disable:no-any */
    public loadClientSideWebPart(
      instanceId: string,
      manifest: IClientSideWebPartManifest<any>,
      data: IWebPartData,
      mode: DisplayMode = DisplayMode.Read): void {

    /* tslint:enable:no-any */
      this._initialize();

      const context: IWebPartManagerContext = {
        domElement: this._findWebPartContainer(instanceId),
        instanceId: instanceId,
        manifest: manifest,
        webPartData: data,
        displayMode: mode
      };

      _TraceLogger.logVerbose(this._logSource, `Loading the web part. manager context = '${JSON.stringify(context)}'.`);
      this._manager.loadWebPart(context);
    }

    /**
     * Log an error in case of loading failure.
     *
     * @param instanceId - Instance id of the webpart e.g. a GUID.
     * @param error - The error string.
     */
    public logClientSideWebPartLoadError(instanceId: string, error: string): void {
        this._initialize();

        this._manager.renderError(this._findWebPartContainer(instanceId), new Error(error));
    };

    /**
     * Global method called by the webpart configure button in the classic page
     *
     * @param instanceId - Instance id of the webpart e.g. a GUID.
     * @param title - The title of the webpart.
     */
    public configureStart (instanceId: string, title: string): void {
      this._initialize();

      _TraceLogger.logVerbose(this._logSource, `Start configuring web part ${title} with id ${instanceId}.`);
      this._manager.configureStart(instanceId, PropertyPaneState.Toggle);
    };

    /**
     * Initialize the classic page bootstrapper.
     */
    private _initialize(): void {
      if (!this._initialized) {
        this._manager = new ClientSideWebPartManager(ClassicPageHost.getInstance());
        this._initialized = true;
        _TraceLogger.logVerbose(this._logSource, `Classic page bootstrapper initialized.`);
      }
    }

    /**
     * Find the web part container DOM element.
     *
     * @param instanceId - Instance id of the webpart e.g. a GUID.
     */
    private _findWebPartContainer(instanceId: string): HTMLElement {
      const element: HTMLElement =
        document.querySelector('[data-sp-cs-webpartid=' + instanceId + ']') as HTMLElement;

      if (!element) {
        _TraceLogger.logError(
          this._logSource,
          new Error(`Unable to find the container DOM element for the web part instanceId ${instanceId}.`));
      }

      return element;
    }
}

// Kickoff the initialization
/* tslint:disable:no-any */
if (!(window as any).g_cswp) {
  (window as any).g_cswp = new ClassicPageBootstrapper();
/* tslint:enable:no-any */
}



