import { IRichTextFormat } from './IRichTextFormat';

import Strings from '../../sp-canvas/loc/CanvasStrings.resx';

/**
 * Static class for SPRte configuratoion
 * Note: We assume all RTE instances share the same configuration by design
 * Should this change in the future, we'll need to change this to non-static design
 */
export default class SPRteConfiguration {
  private _supportedFormatsArray: IRichTextFormat[];
  private _formats: Map<string, IRichTextFormat>;

  constructor() {
    this._supportedFormatsArray = [
      {
        key: 'bold',
        editorKey: 'bold',
        browserKey: 'bold',

        isBlockStyle: false,
        isLine: false,
        isEditorNative: true,

        button: {
          canBeActive: true,
          fabricIconKey: 'Bold',
          shortcut: 'Ctrl+B',
          title: Strings.FormattingBarBoldButtonTitle
        }
      },
      {
        key: 'italic',
        editorKey: 'italic',
        browserKey: 'italic',

        isBlockStyle: false,
        isLine: false,
        isEditorNative: true,

        button: {
          canBeActive: true,
          fabricIconKey: 'Italic',
          shortcut: 'Ctrl+I',
          title: Strings.FormattingBarItalicButtonTitle
        }
      },
      {
        key: 'underline',
        editorKey: 'underline',
        browserKey: 'underline',

        isBlockStyle: false,
        isLine: false,
        isEditorNative: true,

        button: {
          canBeActive: true,
          fabricIconKey: 'Underline',
          shortcut: 'Ctrl+U',
          title: Strings.FormattingBarUnderlineButtonTitle
        }
      },
      {
        key: 'link',
        editorKey: 'link',

        isBlockStyle: false,
        isLine: false,
        isEditorNative: true,
        masks: ['underline'],

        button: {
          canBeActive: true,
          fabricIconKey: 'Link',
          shortcut: 'Ctrl+K',
          title: Strings.FormattingBarLinkButtonTitle
        }
      },
      {
        key: 'alignLeft',
        editorKey: 'align',
        browserKey: 'justifyLeft',

        isBlockStyle: false,
        isLine: true,
        isEditorNative: true,
        editorValue: 'left',

        button: {
          canBeActive: true,
          fabricIconKey: 'AlignLeft',
          title: Strings.FormattingBarAlignLeftButtonTitle
        }
      },
      {
        browserKey: 'justifyCenter',
        key: 'alignCenter',
        editorKey: 'align',

        isBlockStyle: false,
        isLine: true,
        isEditorNative: true,
        editorValue: 'center',

        button: {
          canBeActive: true,
          fabricIconKey: 'AlignCenter',
          title: Strings.FormattingBarAlignCenterButtonTitle
        }
      },
      {
        key: 'alignRight',
        editorKey: 'align',
        browserKey: 'justifyRight',

        isBlockStyle: false,
        isLine: true,
        isEditorNative: true,
        editorValue: 'right',

        button: {
          canBeActive: true,
          fabricIconKey: 'AlignRight',
          title: Strings.FormattingBarAlignRightButtonTitle
        }
      },
      {
        key: 'bullet',
        editorKey: 'bullet',
        browserKey: 'insertUnorderedList',

        isBlockStyle: true,
        isLine: true,
        isEditorNative: true,

        button: {
          canBeActive: true,
          fabricIconKey: 'BulletedList',
          title: Strings.FormattingBarBulletListButtonTitle
        }
      },
      {
        key: 'list',
        editorKey: 'list',
        browserKey: 'insertOrderedList',

        isBlockStyle: true,
        isLine: true,
        isEditorNative: true,

        button: {
          canBeActive: true,
          fabricIconKey: 'NumberedList',
          title: Strings.FormattingBarNumberedListButtonTitle
        }
      },
      {
        key: 'h2',
        editorKey: 'h2',

        isBlockStyle: true,
        isLine: true,
        isEditorNative: false,
        masks: ['bold', 'italic', 'underline'],

        button: {
          canBeActive: true,
          title: Strings.FormattingBarHeading2ButtonTitle,
          label: Strings.FormattingBarHeading2ButtonTitle,
          className: 'ToolbarButton--h2'
        }
      },
      {
        key: 'h3',
        editorKey: 'h3',

        isBlockStyle: true,
        isLine: true,
        isEditorNative: false,
        masks: ['bold', 'italic', 'underline'],

        button: {
          canBeActive: true,
          title: Strings.FormattingBarHeading3ButtonTitle,
          label: Strings.FormattingBarHeading3ButtonTitle,
          className: 'ToolbarButton--h3'
        }
      },
      {
        key: 'h4',
        editorKey: 'h4',

        isBlockStyle: true,
        isLine: true,
        isEditorNative: false,
        masks: ['bold', 'italic', 'underline'],

        button: {
          canBeActive: true,
          title: Strings.FormattingBarHeading4ButtonTitle,
          label: Strings.FormattingBarHeading4ButtonTitle,
          className: 'ToolbarButton--h4'
        }
      },
      {
        key: 'blockquote',
        editorKey: 'blockquote',

        isBlockStyle: true,
        isLine: true,
        isEditorNative: false,
        masks: ['bold', 'italic', 'underline', 'alignLeft', 'alignCenter', 'alignRight'],

        button: {
          canBeActive: true,
          title: Strings.FormattingBarQuoteButtonTitle,
          label: Strings.FormattingBarQuoteButtonTitle,
          className: 'ToolbarButton--blockquote'
        }
      }
    ];

    // Build a map to look up formats by their key quickly
    this._formats = new Map<string, IRichTextFormat>();
    for (const format of this._supportedFormatsArray) {
      this._formats.set(format.key, format);
    }

    // Populate 'maskedBy' field of all formats based on 'masks' fields
    const maskingFormats: IRichTextFormat[] =
      this._supportedFormatsArray.filter((format) => { return !!format.masks; });

    for (const maskingFormat of maskingFormats) {
      for (const maskedFormatKey of maskingFormat.masks) {
        const maskedFormat: IRichTextFormat = this._formats.get(maskedFormatKey);
        if (!maskedFormat.maskedBy) {
          maskedFormat.maskedBy = [maskingFormat.key];
        } else {
          maskedFormat.maskedBy.push(maskingFormat.key);
        }
      }
    }
  }

  public get formats(): Map<string, IRichTextFormat> {
    return this._formats;
  }

  public get supportedFormatsArray(): IRichTextFormat[] {
    return this._supportedFormatsArray;
  }
}



