'use strict';

import { IClientSideWebPartManifestInstance } from '@microsoft/sp-module-interfaces';
import {
  basicHttpClientServiceKey,
  httpClientServiceKey,
  _logSourceServiceKey,
  pageContextServiceKey,
  HttpClient,
  BasicHttpClient,
  PageContext,
  ServiceScope,
  _LogSource,
  Environment,
  environmentServiceKey
} from '@microsoft/sp-client-base';

import ClientSideComponentContext from './ClientSideComponentContext';
import IClientSideWebPartStatusRenderer from './../../common/webParts/IClientSideWebPartStatusRenderer';
import { IEventAggregator } from '../../common/events/IEvent';
import IWebPartContext from './IWebPartContext';
import IWebPartHost from '../../common/webParts/IWebPartHost';
import IWebPartManagerContext from './IWebPartManagerContext';
import { PropertyPaneState } from '../../common/webParts/PropertyPaneDefinitions';

/**
 * Web part context object. This object contains the contextual services available to a web part. e.g.
 * a contextual instance to the http client.
 */
/* tslint:disable:no-any */
export default class WebPartContext
  extends ClientSideComponentContext<IClientSideWebPartManifestInstance<any>>
  implements IWebPartContext {
/* tslint:enable:no-any */

  private _instanceId: string;
  private _webPartTag: string;
  private _httpClient: HttpClient;
  private _basicHttpClient: BasicHttpClient;
  private _pageContext: PageContext;
  private _statusRenderer: IClientSideWebPartStatusRenderer;
  private _eventAggregator: IEventAggregator;
  private _configureStart: (id: string, propertyPaneState?: PropertyPaneState) => void;
  private _serviceScope: ServiceScope;
  private _host: IWebPartHost;
  private _environment: Environment;

  constructor(
    context: IWebPartManagerContext,
    /* tslint:disable:no-any */
    manifestInstance: IClientSideWebPartManifestInstance<any>,
    /* tslint:enable:no-any */
    host: IWebPartHost,
    statusRenderer: IClientSideWebPartStatusRenderer,
    eventAggregator: IEventAggregator,
    configureStart: (id: string, propertyPaneState?: PropertyPaneState) => void,
    webPartTag: string) {

    super(context.domElement, manifestInstance);

    this._instanceId = context.instanceId;
    this._webPartTag = webPartTag;

    this._host = host;
    this._serviceScope = this._createWebPartServiceScope(host.serviceScope);
    this._httpClient = this._serviceScope.consume(httpClientServiceKey);
    this._basicHttpClient = this._serviceScope.consume(basicHttpClientServiceKey);
    this._pageContext = this._serviceScope.consume(pageContextServiceKey);
    this._environment = this._serviceScope.consume(environmentServiceKey);

    this._statusRenderer = statusRenderer;
    this._eventAggregator = eventAggregator;
    this._configureStart = configureStart;
  }

  /**
   * @see IWebPartContext
   */
  public get instanceId(): string { return this._instanceId; }

  /**
   * @see IWebPartContext
   */
  public get webPartTag(): string { return this._webPartTag; }

  /**
   * @see IWebPartContext
   */
  public get httpClient(): HttpClient { return this._httpClient; }

  /**
   * @see IWebPartContext
   */
  public get basicHttpClient(): BasicHttpClient { return this._basicHttpClient; }

  /**
   * @see IWebPartContext
   */
  public get pageContext(): PageContext { return this._pageContext; }

  /**
   * @see IWebPartContext
   */
  public get statusRenderer(): IClientSideWebPartStatusRenderer { return this._statusRenderer; }

  /**
   * @see IWebPartContext
   */
  public get eventAggregator(): IEventAggregator { return this._eventAggregator; }

  /**
   * @see IWebPartContext
   */
  public get configureStart(): (id: string, propertyPaneState?: PropertyPaneState) => void {
    return this._configureStart;
  }

  /**
   * @see IWebPartContext
   */
  public get host(): IWebPartHost { return this._host; }

  /**
   * @see IWebPartContext
   */
  public get serviceScope(): ServiceScope { return this._serviceScope; }

  /**
   * @see IWebPartContext
   */
  public get environment(): Environment { return this._environment; }

  /**
   * Create a web part specific service scope.
   *
   * @param context - Web part manager context
   */
  private _createWebPartServiceScope(parentScope: ServiceScope): ServiceScope {
    const webPartScope: ServiceScope = parentScope.startNewChild();
    webPartScope.createDefaultAndProvide(basicHttpClientServiceKey);
    webPartScope.createDefaultAndProvide(httpClientServiceKey);
    webPartScope.provide(_logSourceServiceKey, _LogSource.create(this._webPartTag));
    webPartScope.finish();
    return webPartScope;
  }
}


