import * as React from 'react';

import { IToolboxGroupProps } from './IToolboxGroup';

import './ToolboxGroup.scss';

export default function ToolboxGroup(props: IToolboxGroupProps): React.ReactElement<IToolboxGroupProps> {
  const banner: React.ReactElement<{}> = props.hasHeader ?
    <header className='CanvasToolboxItemsGroup-header'>{ props.groupName }</header> :
    <hr className='CanvasToolboxItemsGroup-divider' />;
  return (
    <section aria-label={props.groupName} role='group' className='CanvasToolboxItemsGroup'>
      { banner }
      <div className='CanvasToolboxItemsGroup-items'>
        { props.children }
      </div>
    </section>
  );
}


