import  { KeyboardEvent as ReactKeyboardEvent } from 'react';

export enum KeyCodes {
  Enter = 13,
  Escape = 27,
  Tab = 9,
  F10 = 121,
  P = 80
}

export function isEscape(e: KeyboardEvent | ReactKeyboardEvent): boolean {
  return isKey(KeyCodes.Escape, e);
}

export function isEnter(e: KeyboardEvent | ReactKeyboardEvent): boolean {
  return isKey(KeyCodes.Enter, e);
}

export function isShiftTab(e: KeyboardEvent | ReactKeyboardEvent): boolean {
  return isKey(KeyCodes.Tab, e, false, false, true);
}

export function isTab(e: KeyboardEvent | ReactKeyboardEvent): boolean {
  return isKey(KeyCodes.Tab, e);
}

export function isKey(
    keyCode: number, e: KeyboardEvent | ReactKeyboardEvent,
    allowAlt: boolean = false,
    allowCtrl: boolean = false,
    allowShift: boolean = false
  ): boolean {
    return e.keyCode === keyCode &&
      ((allowAlt && e.altKey) || (!allowAlt && !e.altKey)) &&
      ((allowCtrl && e.ctrlKey) || (!allowCtrl && !e.ctrlKey)) &&
      ((allowShift && e.shiftKey) || (!allowShift && !e.shiftKey));
}

export function getFocusableChildren(elem: HTMLElement, ignoreTabIndex: boolean = false): HTMLElement[] {
  const children: HTMLElement[] = [];
  for (const descendant of getFocusableDescendants(elem, ignoreTabIndex)) {
    if (isElementFocusableChildOf(descendant, elem)) {
      children.push(descendant);
    }
  }
  return children;
}

export function isElementFocusable(elem: HTMLElement, ignoreTabIndex: boolean = false): boolean {
  const tagName: string = elem.tagName.toLowerCase();

  if (elem && !elem.hidden && window.getComputedStyle(elem).display !== 'none') {
    const hasTabIndex: boolean = elem.hasAttribute('tabindex');
    const tabindex: number = parseInt(elem.getAttribute('tabindex'), 10);

    if (!ignoreTabIndex && hasTabIndex && tabindex < 0) {
      return false;
    }

    if (
      tagName === 'a' && !!(elem as HTMLAnchorElement).href ||
      elem.isContentEditable
    ) {
      return true;
    }

    if (
      (
        tagName === 'button' ||
        tagName === 'input' ||
        tagName === 'select' ||
        tagName === 'textarea'
      ) && !(elem as HTMLButtonElement).disabled
    ) {
      return true;
    }

    if (!ignoreTabIndex && hasTabIndex && tabindex > -1) {
      return true;
    }
  }

  return false;
}

export function getFocusableParent(elem: HTMLElement, topElement: HTMLElement = document.body): HTMLElement {
  let parent: HTMLElement = elem;

  do {
    parent = parent.parentElement;
  } while (!isElementFocusable(parent) && !parent.isEqualNode(topElement));

  return parent;
}

function queryFocusableSelector(elem: HTMLElement): NodeList {
  const selector: string =
    'button,input,textarea,select,a[href]:not([href=\'\']),\
      [tabindex],[contenteditable=\'true\']';
  return elem.querySelectorAll(selector);
}

export function getFirstFocusableChild(elem: HTMLElement, ignoreTabIndex: boolean = false): HTMLElement {
  const nodes: NodeList = queryFocusableSelector(elem);
  for (let i: number = 0; i < nodes.length; i++) {
    const node: HTMLElement = nodes[i] as HTMLElement;
    if (isElementFocusable(node, ignoreTabIndex)) {
      return node;
    }
  }
  return undefined;
}

export function getFocusableDescendants(elem: HTMLElement, ignoreTabIndex: boolean = false): HTMLElement[] {
  const descendants: HTMLElement[] = [];
  const nodes: NodeList = queryFocusableSelector(elem);
  for (let i: number = 0; i < nodes.length; i++) {
    const node: HTMLElement = nodes[i] as HTMLElement;
    if (isElementFocusable(node, ignoreTabIndex)) {
      descendants.push(node);
    }
  }
  return descendants;
}

export function getFocusableSiblings(elem: HTMLElement, topElement: HTMLElement = document.body): HTMLElement[] {
  const children: HTMLElement[] = getFocusableChildren(getFocusableParent(elem, topElement));
  // Remove input element and start the list from its next sibling
  const siblings: HTMLElement[] = [];
  let insertPointer: number = 0;
  for (const child of children) {
    if (!child.isEqualNode(elem)) {
      siblings.splice(insertPointer++, 0, child);
    } else {
      insertPointer = 0;
    }
  }
  return siblings;
}

export function getNextFocusableElement(elem: HTMLElement, topElement: HTMLElement = document.body): HTMLElement {
  const siblings: HTMLElement[] = getFocusableSiblings(elem, topElement);
  return siblings[0];
}

export function getPrevFocusableElement(elem: HTMLElement, topElement: HTMLElement = document.body): HTMLElement {
  const siblings: HTMLElement[] = getFocusableSiblings(elem, topElement);
  return siblings[siblings.length - 1];
}

export function isElementFocusableChildOf(elem: HTMLElement, ancestor: HTMLElement): boolean {
  let parent: HTMLElement = elem.parentElement;

  while (parent.tagName !== 'BODY') {
    if (parent.isEqualNode(ancestor)) {
      return true;
    }
    if (isElementFocusable(parent)) {
      break;
    }
    parent = parent.parentElement;
  }

  return false;
}

export function navigateInside(elem: HTMLElement): boolean {
  const children: HTMLElement[] = getFocusableChildren(elem);
  if (children.length) {
    children[0].focus();
    return true;
  } else {
    return false;
  }
}

export function navigateTo(elem: HTMLElement): boolean {
  if (isElementFocusable(elem)) {
    elem.focus();
    return true;
  } else {
    return navigateInside(elem);
  }
}

export function navigateOutOf(elem: HTMLElement, topElement: HTMLElement = document.body): void {
  topElement = topElement || document.body;
  getFocusableParent(elem, topElement).focus();
}

export class CanvasA11yManager {
  public static _SKIP_FLAG: string = 'a11y-skip';

  private _canvasElement: HTMLElement;
  private _randId: number;
  private _savedFocusedElement: HTMLElement;

  public constructor(canvasElement: HTMLElement, shouldHandleKeyDown: boolean = true) {
    this._canvasElement = canvasElement;
    this._handleKeyDown = this._handleKeyDown.bind(this);
    this._randId = Math.floor(Math.random() * 1000000);

    if (shouldHandleKeyDown) {
      this._canvasElement.addEventListener('keydown', this._handleKeyDown);
    }
  }

  public ariaAlert(message: string): void {
    const divId: string = 'aria_alert_' + this._randId;
    const oldAlert: HTMLElement = document.querySelector('#' + divId) as HTMLElement;
    if (oldAlert) {
      document.body.removeChild(oldAlert);
    }

    const alertNode: HTMLElement = document.createElement('p');
    alertNode.classList.add('Canvas-screenReaderOnly');
    alertNode.setAttribute('role', 'alert');
    alertNode.setAttribute('id', divId);
    const alertText: Node = document.createTextNode(message);
    alertNode.appendChild(alertText);
    document.body.appendChild(alertNode);
  }

  public navigateInside(elem: HTMLElement): boolean {
    return navigateInside(elem);
  }

  public navigateTo(elem: HTMLElement): boolean {
    return navigateTo(elem);
  }

  public navigateOutOf(elem: HTMLElement): void {
    navigateOutOf(elem);
  }

  public clearState(): void {
    this._savedFocusedElement = undefined;
  }

  public restoreState(): boolean {
    return this.navigateTo(this._savedFocusedElement);
  }

  public saveState(elem?: HTMLElement): void {
    elem = elem || document.activeElement as HTMLElement;
    this._savedFocusedElement = elem;
  }

  public skipEvent(e: KeyboardEvent): void {
    (e as Object)[CanvasA11yManager._SKIP_FLAG] = true;
  }

  private _shouldSkipEvent(e: KeyboardEvent): boolean {
    return (e as Object)[CanvasA11yManager._SKIP_FLAG] as boolean;
  }

  private _handleKeyDown(e: KeyboardEvent): void {
    if (this._shouldSkipEvent(e)) {
      return;
    }

    const currentElement: HTMLElement = isElementFocusable(e.target as HTMLElement) ?
      e.target as HTMLElement : getFocusableParent(e.target as HTMLElement, this._canvasElement);

    let elementToGo: HTMLElement;
    if (isTab(e)) {
      const children: HTMLElement[] = getFocusableChildren(this._canvasElement);
      if (children.length && !currentElement.isEqualNode(children[children.length - 1])) {
        elementToGo = getNextFocusableElement(currentElement, this._canvasElement);
      }
    } else if (isShiftTab(e)) {
      const children: HTMLElement[] = getFocusableChildren(this._canvasElement);
      if (children.length && !currentElement.isEqualNode(children[0])) {
        elementToGo = getPrevFocusableElement(currentElement, this._canvasElement);
      }
    } else if (isEnter(e)) {
      const children: HTMLElement[] = getFocusableChildren(currentElement);
      elementToGo = children.length ? children[0] : undefined;
    } else if (isEscape(e)) {
      elementToGo = getFocusableParent(currentElement, this._canvasElement);
    }

    if (elementToGo) {
      elementToGo.focus();
      e.preventDefault();
      e.stopPropagation();
    }
  }
}



