import { IBounds } from '../rte/IBounds';
import { IRange } from '../rte/IRange';
import { IRichTextFormat } from '../rte/IRichTextFormat';
import { IRichTextEditor } from '../rte/IRichTextEditor';
import { IRichTextEditorLoader } from '../rte/IRichTextEditorLoader';
import QuillInstance = require('QuillInstance');
import QuillLoader from './QuillLoader';
import SPRteConfiguration from '../rte/SPRteConfiguration';

import Strings from '../../sp-canvas/loc/CanvasStrings.resx';

/**
 * Implmentation of ISPRteQuillIntegration for Quill 0.20.1
 */
export default class QuillEditor implements IRichTextEditor {
  public static moduleLoader: IRichTextEditorLoader = new QuillLoader();

  private static quillDelta: QuillDelta;
  private static quillDocument: QuillDocument;

  private _config: SPRteConfiguration;
  private _editorElement: HTMLDivElement;
  private _quill: QuillInstance;
  private _quillEditorElement: HTMLElement;
  private _handleSelectionChange: (range: IRange) => void;
  private _handleTextChange: () => void;

  /**
   * @return Promise of the instance that resolves once the editor is loaded on the element
   *
   * Static wrapper for constructing an instance
   */
  public static createNewEditor(
      config: SPRteConfiguration,
      currentHTML: string,
      editorElement: HTMLDivElement,
      selectionChangeCallback: (range: IRange) => void,
      textChangeCallback: () => void
    ): Promise<QuillEditor> {
    return new Promise<QuillEditor>(
      (resolve?: (result: QuillEditor) => void) => {
        QuillEditor.moduleLoader.loadModule().then(() => {
          const editor: QuillEditor = new QuillEditor(
            config,
            currentHTML,
            editorElement,
            selectionChangeCallback,
            textChangeCallback
          );
          resolve(editor);
        });
      }
    );
  }

  /**
   * Use createNewEditor instead of constructor to ensure using the editor after it's loaded
   * Note: TypeScript doesn't allow private constructors
   */
  public constructor(
    config: SPRteConfiguration,
    currentHTML: string,
    editorElement: HTMLDivElement,
    selectionChangeCallback: (range: IRange) => void,
    textChangeCallback: () => void
  ) {
    this._config = config;
    this._editorElement = editorElement;
    this._findWrappingElementByTagName = this._findWrappingElementByTagName.bind(this);
    this._handleSelectionChange = selectionChangeCallback;
    this._handleTextChange = textChangeCallback;

    this.focus = this.focus.bind(this);
    this.format = this.format.bind(this);
    this.getSelection = this.getSelection.bind(this);

    this._initialize(currentHTML);
  }

  public destroy(): string {
    if (this._quill) {
      const previousHTML: string = this._quill.destroy();
      this._quill = undefined;
      return previousHTML;
    }

    return undefined;
  }

  /**
   * @param start - Start position of selection
   * @param end - End position of selection
   * @param formats - Map of key:value pairs for formats to apply
   * @param applyAllFormatsToLine - Forces all formats to be applied to the whole line(s). If not provided,
   * formats are divided into two buckets (line vs non-line) based on their isLine field
   *
   * A unified method for applying Quill formats to any selection
   */
  public format(start: number, end: number, formats: Object, applyAllFormatsToLine?: boolean): void {
    const lineFormats: Object = applyAllFormatsToLine ? formats : {};
    const nonLineFormats: Object = {};

    if (!applyAllFormatsToLine) {
      // Divide formats into two buckets: Line and Non-Line
      /* tslint:disable */
      for (const formateditorKey in formats) {
      /* tslint:enable */
        const format: IRichTextFormat = this._config.supportedFormatsArray
            .filter((format) => { return format.editorKey === formateditorKey; })[0];
        if (format.isLine) {
          lineFormats[format.editorKey] = formats[format.editorKey];
        } else {
          nonLineFormats[format.editorKey] = formats[format.editorKey];
        }
      }
    }

    /* Apply line formats: If there is a newline character at the end of the selection, Quill.formatLine
    formats an extra line. We don't want the extra line by design so we remove the last character if
    it's a newline character */
    const firstLineOffset: number = this._quill.editor.doc.findLineAt(start)[1];
    const [lastLine, lastLineOffset]: [QuillLine, number] = this._quill.editor.doc.findLineAt(end);
    const startOfFirstLine: number = start - firstLineOffset;
    const endOfLastLine: number =
      (start !== end && lastLineOffset === 0) ? end - 1 : end + lastLine.length - lastLineOffset - 1;
    this._quill.formatLine(startOfFirstLine, endOfLastLine, lineFormats);

    if (!applyAllFormatsToLine) {
      // Apply Non-Line formats
      /* tslint:disable */
      if (nonLineFormats.hasOwnProperty('link') &&
        this.isFormatAppliedToRange(start, end, this._config.formats.get('link'))) {
        // There is already a link on the selection, remove or edit it
        if (start === end) {
          const linkElement: HTMLElement = this._findWrappingElementByTagName(start, 'A');
          if (linkElement) {
            if (nonLineFormats['link'] as boolean) {
              linkElement.setAttribute('href', nonLineFormats['link']);
              linkElement.setAttribute('target', '_blank');
            } else {
              const container: HTMLElement = linkElement.parentElement;
              if (container) {
                const textNode: Text = document.createTextNode(linkElement.textContent);
                container.insertBefore(textNode, linkElement);
                container.removeChild(linkElement);
              }
            }
            nonLineFormats['link'] = undefined;
          }
        } else if(nonLineFormats['link'] as boolean) {
          // Edit link on non-collapsed selection
          let linkElement: HTMLElement;
          const linkAtEnd: HTMLElement = this._findWrappingElementByTagName(end, 'A');
          // If the selection size is larger than 1, make sure the whole selection is on the same link
          if (end - start > 1) {
            const linkAtStart: HTMLElement = this._findWrappingElementByTagName(start + 1, 'A');
            linkElement = linkAtStart && linkAtEnd && linkAtStart.isEqualNode(linkAtEnd) ? linkAtEnd : undefined;
          } else {
            linkElement = linkAtEnd;
          }

          if (linkElement) {
            linkElement.setAttribute('href', nonLineFormats['link']);
            linkElement.setAttribute('target', '_blank');
            nonLineFormats['link'] = undefined;
          }
        }
      }

      if (start === end) {
        for (let format in nonLineFormats) {
          if (format === 'bold' || format === 'italic' || format === 'underline') {
            this._quill.prepareFormat(format, nonLineFormats[format]);
          }
        }
      } else {
        this._quill.formatText(start, end, nonLineFormats);

        if (nonLineFormats.hasOwnProperty('link') && nonLineFormats['link'] as boolean) {
          const linkElement: HTMLElement = this._findWrappingElementByTagName(end, 'A');
          if (linkElement) {
            linkElement.setAttribute('target', '_blank');
          }
        }
      }
    }
    /* tslint:enable */
  }

  public getBounds(position: number): IBounds {
    return this._quill.getBounds(position);
  }

  public getSelection(): IRange {
    return this._quill.getSelection();
  }

  public getText(range?: IRange): string {
    return range ?
      this._quill.getText(range.start, range.end) :
      this._quill.getText();
  }

  public getHTML(): string {
    return this._quill.getHTML();
  }

  public getFormatValue(start: number, end: number, format: IRichTextFormat): boolean | string {
    if (format.key === 'link') {
      let linkElement: HTMLElement;
      const linkAtEnd: HTMLElement = this._findWrappingElementByTagName(end, 'A');
      // If the selection size is larger than 1, make sure the whole selection is on the same link
      if (end - start > 1) {
        const linkAtStart: HTMLElement = this._findWrappingElementByTagName(start + 1, 'A');
        linkElement = linkAtStart && linkAtEnd && linkAtStart.isEqualNode(linkAtEnd) ? linkAtEnd : undefined;
      } else {
        linkElement = linkAtEnd;
      }
      return linkElement ? linkAtEnd.getAttribute('href') : undefined;
    } else {
      return this.isFormatAppliedToRange(start, end, format);
    }
  }

  /**
   * @param format - Name of the format to apply from QUILL_FORMATS or SPRTE_FORMATS
   * @param start - Start value of range
   * @param end - End value of range
   *
   * @return A dictionary of string=>boolean indicating if the format is applied to the given range
   */
  public isFormatAppliedToRange(start: number, end: number, format: IRichTextFormat): boolean {
    let canBrowserHandle: boolean = !!format.browserKey;

    /* Chrome has a bug that returns true for bold, italic, underline if an empty line is selected
    We temporarily add this block to use Quill for those formats until the issue is fixed.
    Issue filed at: https://bugs.chromium.org/p/chromium/issues/detail?id=606047 */
    if (start !== end && ['bold', 'italic', 'underline'].indexOf(format.key) !== -1) {
      canBrowserHandle = false;
    }

    // IE and FF don't support justification commands, use Quill API instead
    if (['alignCenter', 'alignLeft', 'alignRight'].indexOf(format.key) !== -1 ) {
      const line: QuillLine = this._quill.editor.doc.findLineAt(end)[0];
      if (line.delta.ops.length > 0) {
        /* tslint:disable */
        // The align attribute is always set in last delta attributes because it contains the newline character
        const attributes: any = line.delta.ops[line.delta.ops.length - 1].attributes;
        // @todo #188988: default alignment should match locale (LTR vs RTL)
        const textAlign: string = attributes && attributes.align ? attributes.align : 'left';
        /* tslint:disable */

        if (format.key === 'alignCenter') {
           return textAlign === 'center';
        } else if (format.key === 'alignLeft') {
           return textAlign === 'left';
        } else if (format.key === 'alignRight') {
           return textAlign === 'right';
        }
      }
    }

    // If the format can't be handled by browser use Quill to find active formats
    if (!canBrowserHandle) {
      // If the selection is collapsed, check the last character
      start = start === end ? Math.max(0, end - 1) : start;
      const activeFormats: Map<string, number> = this._getActiveFormatsInRange(start, end, format.isLine);
      return (format.isLine || start === 0) ?
        !!activeFormats.get(format.key) :
        activeFormats.get('count') === activeFormats.get(format.key);
    } else {
      return document.queryCommandState(format.browserKey);
    }
  }

  public setSelection(range: IRange): void {
    this._quill.setSelection(range.start, range.end);
  }

  public focus(): void {
    // Use setActive() instead of focus() if available, because focus() scrolls to view in IE
    if (this._quillEditorElement && this._quillEditorElement.setActive) {
      this._quillEditorElement.setActive();
    } else {
      this._quill.focus();
    }
  }

  private _findWrappingElementByTagName(position: number, tagName: string): HTMLElement {
    const leafNode: Node = this._quill.editor.doc.findLeafAt(Math.max(0, position - 1))[0].node;

    /* Move up the dom tree until find the first node with the given tag name or reach the editor element
    Note: Use .parentNode instead of .parentElement, because IE doesn't have .parentElement on text nodes */
    let parentNode: HTMLElement = leafNode.parentNode as HTMLElement;
    while (parentNode && parentNode.tagName !== tagName && !parentNode.isEqualNode(this._editorElement)) {
      parentNode = parentNode.parentNode as HTMLElement;
    }

    return parentNode.tagName === tagName ? parentNode : undefined;
  }

  /**
   * @param start - start value of a selection
   * @param end - end value of a selection
   *
   * Find and calculate all the non-sequential formattings in a selection.
   *
   * @return A dictionary mapping of format to non-sequential occurrences in the range of start to end
   */
  private _getActiveFormatsInRange(start: number, end: number, isLine: boolean = false): Map<string, number> {
    start = start === end ? Math.max(0, end - 1) : start;
    /* tslint:disable:no-any */
    let contentAttributes: any[];
    /* tslint:enable:no-any */

    if (isLine) {
      const line: QuillLine = this._quill.editor.doc.findLineAt(end)[0];
      contentAttributes = line ? line.delta.ops : [];
    } else {
      contentAttributes = this._quill.getContents(start, end).ops;
    }

    /* The length of contentAttributes is the number of non-sequential formatting parts in the selection.
       Keeping track of count for the special case of formatting an entire selection where only a portion
       of the selection is formatted with a given format. */
    const attrDict: Map<string, number> = new Map<string, number>([['count', contentAttributes.length]]);

    const formats: string[] = this._config.supportedFormatsArray.map((format) => { return format.key; });
    for (const format of formats) {
      attrDict.set(format, 0);
    }

    for (const op of contentAttributes) {
      // op.attributes can be undefined if the chunk is unformatted
      if (op.attributes) {
        for (const attr in op.attributes) {
          // Verify the attribute is a recognized format
          if (attrDict.has(attr)) {
            attrDict.set(attr, attrDict.get(attr) + 1);
          }
        }
      }
    }

    return attrDict;
  }

  /**
   * Initialize Quill with whitelist of formats, prevent default css,
   * add custom formats, and registering event handlers.
   */
  private _initialize(currentHTML: string): void {
    QuillEditor.moduleLoader.loadModule().then((quillModule) => {
      if (!QuillEditor.quillDelta) {
        QuillEditor.quillDelta = quillModule.require('delta') as QuillDelta;
      }

      if (!QuillEditor.quillDocument) {
        QuillEditor.quillDocument = quillModule.require('document') as QuillDocument;
      }

      const quillFormatKeys: string[] = this._config.supportedFormatsArray
        .filter((format) => { return format.isEditorNative; })
        .map((format) => { return format.editorKey; })
        .filter((formatKey, index, self) => { return self.indexOf(formatKey) === index; });  // Distinct keys

      const quill: QuillInstance = new quillModule(this._editorElement, {
        formats: quillFormatKeys,
        readOnly: false,
        styles: false,
        modules: {
          'paste-manager': {
            onConvert: function(container: HTMLDivElement) {
              // Remove images
              const wordImages: NodeList = container.querySelectorAll('img, .MsoNormal [src]:not([src=\'\']');
              for (let i: number = 0; i < wordImages.length; i++) {
                wordImages[i].parentElement.removeChild(wordImages[i]);
              }

              const doc: QuillDocument = new QuillEditor.quillDocument(container, {
                formats: quillFormatKeys,
                readOnly: false,
                styles: false
              });

              // Clean up children with no text content
              let line: QuillLine = doc.lines.first;
              while (line) {
                const next: QuillLine = line.next;
                if (!line.data.textContent.length) {
                  doc.removeLine(line);
                }
                line = next;
              }

              const delta: QuillDelta = doc.toDelta();

              // Remove added newline if applicable
              const lengthAdded: number = delta.length();
              if (lengthAdded === 0) {
                return delta;
              }
              return delta.compose(new QuillEditor.quillDelta().retain(lengthAdded - 1)['delete'](1));
            }
          }
        }
      });

      this._quillEditorElement = this._editorElement.querySelector('.ql-editor') as HTMLElement;
      // @todo 181437, get placeholder text via props later

      this._quillEditorElement.setAttribute('data-placeholder', Strings.TextWebPartPlaceholder);

      // Add custom non-Quill formats to Quill
      // @todo 198188: Do this using _supportedFormatsArray after upgrading to Quill 1.0
      quill.addFormat('blockquote', {
        class: 'quote-',
        type: 'line'
      });

      quill.addFormat('h2', {
        tag: 'H2',
        type: 'line'
      });

      quill.addFormat('h3', {
        tag: 'H3',
        type: 'line'
      });

      quill.addFormat('h4', {
        tag: 'H4',
        type: 'line'
      });

      quill.on('text-change', this._handleTextChange);
      quill.on('selection-change', this._handleSelectionChange);

      quill.setHTML(currentHTML);

      this._quill = quill;
    });
  }
}



