import { IPropertyPaneChoiceGroupProps } from './IPropertyPaneChoiceGroup';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Choice Group on the PropertyPane.
 * @param targetProperty - Target property the choice group is associated to.
 * @param properties - Strongly typed Choice Group properties.
 */
export function PropertyPaneChoiceGroup(
  targetProperty: string,
  properties: IPropertyPaneChoiceGroupProps): IPropertyPaneField<IPropertyPaneChoiceGroupProps> {
  return {
    type: IPropertyPaneFieldType.ChoiceGroup,
    targetProperty: targetProperty,
    properties: properties
  };
}



