import * as React from 'react';
import * as ReactDom from 'react-dom';
import { DisplayMode } from '@microsoft/sp-client-base';
import { StringHelper } from '@ms/odsp-utilities-bundle';

import { IBounds } from './IBounds';
import { ICanvasContext } from '../../sp-canvas/canvas/canvasComponent/ICanvasComponent';
import ISerializedCanvasControl from '../../canvas/canvas/ISerializedCanvasControl';
import { IRange } from './IRange';
import { IRichTextEditor } from './IRichTextEditor';
import { IRichTextFormat } from './IRichTextFormat';
import SPRteConfiguration from './SPRteConfiguration';
import SPRteFormattingBar, {
  ISPRteFormattingBarProps,
  ISPRteFormattingBarPosition
} from '../formattingbar/SPRteFormattingBar';
import SPRteLinkDialog from '../formattingbar/SPRteLinkDialog';
import QuillEditor from '../quill/QuillEditor';
import ToolbarButton, { ButtonState, IToolbarButtonProps } from '../../canvas/canvas/ToolbarButton';
import ToolbarSubmenu, { IToolbarSubmenuProps } from '../../canvas/canvas/ToolbarSubmenu';
import {
  isEscape,
  isKey,
  KeyCodes
} from '../../sp-canvas/a11y/CanvasA11y';

import './SPRte.scss';
import Strings from '../../sp-canvas/loc/CanvasStrings.resx';

export interface ISPRteProps extends ISerializedCanvasControl {
  innerHTML?: string;
}

export interface ISPRteState {
  currentSubmenuOpen?: 'alignment' | 'format' | 'list';
  displayMode?: DisplayMode;
  formattingBarPosition?: ISPRteFormattingBarPosition;
  isFormattingBarHidden?: boolean;
  isLinkDialogOpen?: boolean;
}

/**
 * @class Rich Text Editor class. This component wraps around all the RTE logic including the text editor
 * and the formatting bar and is used as a canvas control.
 */
export default class SPRte extends React.Component<ISPRteProps, ISPRteState> {
  public static contextTypes: React.ValidationMap<SPRte> = {
    store: React.PropTypes.object.isRequired
  };

  /**
   * @ readOnly
   */
  private static _TOOLBAR_DEFAULT_POSITION: ISPRteFormattingBarPosition = {
    left: 0,
    top: -36,
    position: 'absolute'
  };

  /* tslint:disable:max-line-length */
  private static _URL_VALIDATION_REGEX: RegExp = new RegExp('((^https?:\/\/)|(^ftp:\/\/)|(^file:\/\/)|(^mailto:)|(^news:)|(^pnm:\/\/)|(^mms:\/\/)|(^\/)|(^#)|(^\\\\)).+', 'i');
  /* tslint:enable:max-line-length */
  /**
   * @ readOnly
   *
   * The scrollOffset that works for a full-height canvas
   */
  private static _BASE_SCROLL_THRESHOLD: number = -10;

  /**
   * @ readOnly
   */
  private static _CANVASZONE_PADDING_HORIZONTAL: number = 8;

  public context: ICanvasContext;

  private _config: SPRteConfiguration;
  private _currentScrollTop: number;
  private _currentSelection: IRange;
  private _domElement: HTMLDivElement;
  private _editor: IRichTextEditor;
  private _editorElement: HTMLDivElement;
  private _editableDiv: HTMLElement;
  private _formattingbar: SPRteFormattingBar;
  private _formattingbarElement: HTMLElement;
  private _formattingbarWidth: number;

  private _buttonStates: Map<string, ButtonState>;

  private _linkAddress: string;
  private _linkDialog: SPRteLinkDialog;
  private _linkElement: HTMLElement;

  /**
   * _previousHTML is set when a the _quill instance is destroyed.
   * We destroy the quill editor when SPRte is in read mode. The Quill HTML content is used to create a new
   * Quill instance if SPRte is switched from edit -> read -> edit modes in one session.
   */
  private _previousHTML: string;
  /**
   * This stores reference to .ql-editor element to avoid finding it on every render
   */

  private _restoreSelectionOnNextFocus: boolean;
  private _restoreScrollTopOnNextFocus: boolean;

  private _selectionInitiated: boolean;
  private _selectionShouldEnd: boolean;

  constructor(props: ISPRteProps) {
    super(props);

    this._checkSelectionChange = this._checkSelectionChange.bind(this);
    this._getFormatButtonProps = this._getFormatButtonProps.bind(this);
    this._handleAlignCenter = this._handleAlignCenter.bind(this);
    this._handleAlignLeft = this._handleAlignLeft.bind(this);
    this._handleAlignRight = this._handleAlignRight.bind(this);
    this._handleBold = this._handleBold.bind(this);
    this._handleClearFormatting = this._handleClearFormatting.bind(this);
    this._handleEditorFocus = this._handleEditorFocus.bind(this);
    this._handleHeading2 = this._handleHeading2.bind(this);
    this._handleHeading3 = this._handleHeading3.bind(this);
    this._handleHeading4 = this._handleHeading4.bind(this);
    this._handleItalic = this._handleItalic.bind(this);
    this._handleLink = this._handleLink.bind(this);
    this._handleListBullets = this._handleListBullets.bind(this);
    this._handleListNumbered = this._handleListNumbered.bind(this);
    this._handlePlainText = this._handlePlainText.bind(this);
    this._handleQuote = this._handleQuote.bind(this);
    this._handleSelectionChange = this._handleSelectionChange.bind(this);
    this._handleTextChange = this._handleTextChange.bind(this);
    this._handleUnderline = this._handleUnderline.bind(this);
    this._handleUnlink = this._handleUnlink.bind(this);

    this._handleFormattingBarKeyDown = this._handleFormattingBarKeyDown.bind(this);
    this._handleKeyDown = this._handleKeyDown.bind(this);
    this._handleKeyUp = this._handleKeyUp.bind(this);
    this._handleScroll = this._handleScroll.bind(this);
    this._restoreOnFocus = this._restoreOnFocus.bind(this);

    this._onLinkDialogSaved = this._onLinkDialogSaved.bind(this);
    this._openLinkDialog = this._openLinkDialog.bind(this);
    this._closeLinkDialog = this._closeLinkDialog.bind(this);

    this.state = {
      displayMode: this.props.displayMode,
      formattingBarPosition: undefined,
      isFormattingBarHidden: true,
      isLinkDialogOpen: false
    };

    this._config = new SPRteConfiguration();
    this._restoreScrollTopOnNextFocus = false;
    this._restoreSelectionOnNextFocus = false;
    this._buttonStates = new Map<string, ButtonState>();
    this._selectionInitiated = false;
    this._selectionShouldEnd = false;

    if (this.state.displayMode === DisplayMode.Edit && !this._editor) {
      QuillEditor.moduleLoader.loadModule();
    }
  }

  public render(): React.ReactElement<ISPRteProps> {
    let formattingBar: React.ReactElement<ISPRteFormattingBarProps>;

    let html: string;
    let className: string;
    if (this.state.displayMode === DisplayMode.Read) {
      className = 'ql-container';
      html = this._previousHTML || this.props.innerHTML;
    }

    // Do not render a formatting bar if the control is not in edit mode and the
    // formatting bar is hidden
    const isEditMode: boolean = this.state.displayMode === DisplayMode.Edit;
    if (isEditMode && !this.state.isFormattingBarHidden) {
      formattingBar = <SPRteFormattingBar
        buttons={this._formattingBarButtons}
        position={this.state.formattingBarPosition}
        onKeyDown={this._handleFormattingBarKeyDown}
        ref={(c: SPRteFormattingBar) => this._formattingbar = c}
      />;
    }

    return (
      <div ref={(c: HTMLDivElement) => this._domElement = c }>
        <div
          aria-role={ isEditMode ? 'textbox' : undefined }
          aria-multiline={ isEditMode || undefined }
          dangerouslySetInnerHTML={{__html: html}}
          className={ className }
          onKeyUp={ this._handleKeyUp }
          ref={ (c: HTMLDivElement) => this._editorElement = c }
        />
        { formattingBar }
        <SPRteLinkDialog
          editorElement={this._editableDiv}
          isOpen={this.state.isLinkDialogOpen}
          onCancelClicked={this._closeLinkDialog}
          onDismiss={this._closeLinkDialog}
          onSaveClicked={this._onLinkDialogSaved}
          ref={(c: SPRteLinkDialog) => this._linkDialog = c}
          textFieldValue={this._linkAddress || ''}
          validationRegex={SPRte._URL_VALIDATION_REGEX}
        />
      </div>
    );
  }

  public componentDidMount(): void {
    if (this.state.displayMode === DisplayMode.Edit) {
      this._createNewEditor(this.props.innerHTML || '').then((editor) => {
        this._afterEditorCreated(editor);
        this.context.store.webPartManager.configureStart(undefined);
        this.focus();
      });
    }
  }

  public componentDidUpdate(prevProps: ISPRteProps, prevState: ISPRteState): void {
    if (this._formattingbar) {
      this._formattingbarElement = ReactDom.findDOMNode<HTMLElement>(this._formattingbar);
      this._formattingbarWidth = this._formattingbarElement.offsetWidth || this._formattingbarWidth;

      if (this.context.store.scrollableParent) {
        this.context.store.scrollableParent.addEventListener('scroll', this._handleScroll);
      }

      /* If formattingbar exists and editable div doesn't have focus, make the editable div un-tabbable
        This is because we want tab to switch between the toolbars and only ESC can set the focus back to editor */
      const tabIndex: string =
        (this._editableDiv && this._editableDiv.isEqualNode(document.activeElement)) ? '0' : '-1';
      this._editableDiv.setAttribute('tabindex', tabIndex);
    } else {
      this._formattingbarElement = undefined;
      if (this.context.store.scrollableParent) {
        this.context.store.scrollableParent.removeEventListener('scroll', this._handleScroll);
      }
      if (this._editableDiv) {
        this._editableDiv.setAttribute('tabindex', '0');
      }
    }
  }

  public componentWillUnmount(): void {
    if (this._editor) {
      this._previousHTML = this._editor.destroy();
      this._editor = undefined;
    }
  }

  public componentWillUpdate(nextProps: ISPRteProps, nextState: ISPRteState): void {
    if (this.state.displayMode !== nextState.displayMode) {
      if (nextState.displayMode === DisplayMode.Edit) {
        /**
         * Switching from Read mode to Edit mode. Create a new editor
         * If SPRte has previously been in edit mode during the same session we want to use the saved HTML.
         * The props value is used if the editor is switching to edit mode for the first time since
         * _previousHTML will be null.
         */
        this._createNewEditor(this._previousHTML || this.props.innerHTML || '').then((editor) => {
          this._afterEditorCreated(editor);
          this._setButtonStates();
        });

      } else if (nextState.displayMode === DisplayMode.Read) {
        // Switching from Edit mode to Read mode. Remove the editor instance
        this.componentWillUnmount();
      }
    } else if (nextState.displayMode === DisplayMode.Edit && this._editor) {
      /* Re-evaluate button activeness map before every render
      We assume that we only need to change activeness if the focus is inside the editor
      IMPORTANT: this._quill.focus() should not be called during state-transition, because it will trigger
      selection-change and cause another state-transition which is forbidden by React */
      this._setButtonStates();
    }
  }

  public focus(): void {
    if (this._editableDiv) {
      // If focus() is explicitly called on the React component, force the editor to be tabbable
      this._editableDiv.setAttribute('tabindex', '0');
    }

    this._editor.focus();
  }

  public setDisplayMode(newMode: DisplayMode): void {
    if (this.state.displayMode !== newMode) {
      this.setState({
        displayMode: newMode,
        formattingBarPosition: undefined,
        isFormattingBarHidden: true,
        isLinkDialogOpen: false
      });
    }
  }

  /**
   * Return the serialized instance of this control
   */
  public serialize(): ISerializedCanvasControl {
    const serialization: ISPRteProps = JSON.parse(JSON.stringify(this.props));
    serialization.innerHTML = this._editor ? this._editor.getHTML() : this.props.innerHTML;

    return serialization;
  }

  private _afterEditorCreated(editor: IRichTextEditor): void {
    this._editor = editor;
    this._handleTextChange();
    this._editorElement.addEventListener('keydown', this._handleKeyDown);
    document.addEventListener('mouseup', this._checkSelectionChange);
    this._editableDiv = this._editorElement.querySelector('.ql-editor') as HTMLElement;
    if (this._editableDiv) {
      this._editableDiv.setAttribute('aria-label', Strings.RichTextEditorAriaLabel);
      this._editableDiv.addEventListener('focus', this._handleEditorFocus);
    }
  }

  private _ariaConfirmAction(formatKeyOrName: string): void {
    const format: IRichTextFormat = this._config.formats.get(formatKeyOrName);
    const name: string = format ? format.button.title : formatKeyOrName;

    let message: string = StringHelper.format(Strings.FormattingBarConfirmAction, name);
    if (this._currentSelection &&  this._currentSelection.start !== this._currentSelection.end &&
      !(format && format.isLine)) {
      const text: string = this._editor.getText(this._currentSelection);
      message = StringHelper.format(Strings.FormattingBarConfirmActionOnSelection, name, text);
    }
    this.context.store.a11yManager.ariaAlert(message);
}

 /**
  * Note: keyup and mouseup can happen before or after selection-change depending on the user input
  * That's why we need to mark these events in separate flags (_selectionInitiated and _selectionShouldEnd)
  * Whenever one of them happens, we check if the other one has happened so we could complete the action
  *
  * Checks if a selection change should complete, then:
  * - If the selection was already initiated, completes it
  * - Otherwise, markes the selection to be completed after initiation
  */
  private _checkSelectionChange(): void {
    if (this._editor) {
      this._selectionShouldEnd = true;
      if (this._selectionInitiated) {
        const range: IRange = this._editor.getSelection();
        this._currentSelection = range;
        this._updateFormattingBar();
        // Reset completion flag because selection change is already handled
        this._selectionShouldEnd = false;
      }
      // Reset initiation flag for future events
      this._selectionInitiated = false;
    }
  }

  private _closeLinkDialog(): void {
    this._restoreSelectionOnNextFocus = true;
    this._restoreScrollTopOnNextFocus = true;
    this._currentScrollTop = this.context.store.scrollableParent.scrollTop;
    this._linkAddress = '';

    this.setState({
      isLinkDialogOpen: false
    });
  }

  private _createNewEditor(innerHTML: string): Promise<QuillEditor> {
    return QuillEditor.createNewEditor(
      this._config,
      innerHTML,
      this._editorElement,
      this._handleSelectionChange,
      this._handleTextChange
    );
  }

  private _shouldFormattingBarStick(): boolean {
    const editorRelativeTop: number =
      this._editorElement.getBoundingClientRect().top - this.context.store.canvasElement.getBoundingClientRect().top;

    return this.context.store.scrollableParent.scrollTop >
      this.context.store.scrollThreshold + SPRte._BASE_SCROLL_THRESHOLD +
      editorRelativeTop + SPRte._TOOLBAR_DEFAULT_POSITION.top;
  }

  private _getFormattingBarPosition(): ISPRteFormattingBarPosition {
    let left: number = SPRte._TOOLBAR_DEFAULT_POSITION.left;
    let top: number = SPRte._TOOLBAR_DEFAULT_POSITION.top;
    let position: string = 'absolute';
    let isFloating: boolean = false;

    if (this._formattingbarElement) {
      if (this._currentSelection && this._currentSelection.start !== this._currentSelection.end) {
        // Use range.end - 1, because getBounds returns left:0 when the selection ends at the end of line
        const startBounds: IBounds = this._editor.getBounds(this._currentSelection.start);
        const endBounds: IBounds = this._editor.getBounds(Math.max(0, this._currentSelection.end - 1));

        if (startBounds.top === endBounds.top) {
          isFloating = true;
          // Fix the problem where Quill returns faulty endBounds.left when the selection ends close to end of line
          endBounds.left = startBounds.left <= endBounds.left ? endBounds.left : this._editorElement.offsetWidth;
          const toolbarLeft: number =
            (startBounds.left + endBounds.left) / 2 - this._formattingbarWidth / 2;
          const canvasZoneWidth: number = this._editorElement.offsetWidth + SPRte._CANVASZONE_PADDING_HORIZONTAL * 2;

          left = toolbarLeft + this._formattingbarWidth <= canvasZoneWidth ?
            Math.max(toolbarLeft, SPRte._TOOLBAR_DEFAULT_POSITION.left) :
            canvasZoneWidth - this._formattingbarWidth;

          // If the floating top is too close to the fixed top (<line-height), use the fixed top to avoid funkiness
          top = startBounds.top < 23 ?
            SPRte._TOOLBAR_DEFAULT_POSITION.top :
            startBounds.top + SPRte._TOOLBAR_DEFAULT_POSITION.top;

          position = 'absolute';
        }
      }
    }

    // Even if the formatting bar element doesn't exist, we can still calculate the sticky bar position
    if (!isFloating && this._shouldFormattingBarStick()) {
      left = SPRte._TOOLBAR_DEFAULT_POSITION.left +
        this._editorElement.getBoundingClientRect().left - SPRte._CANVASZONE_PADDING_HORIZONTAL;
      top = this.context.store.canvasElement.getBoundingClientRect().top +
        this.context.store.scrollableParent.scrollTop - this.context.store.scrollThreshold;
      position = 'fixed';
    }

    return {left, top, position};
  }

  private _getFormatButtonProps(formatKey: string, onClick: () => void): IToolbarButtonProps {
    const format: IRichTextFormat = this._config.formats.get(formatKey);
    return {
      key: format.key,
      fabricIconKey: format.button.fabricIconKey,
      iconClassName: format.button.iconClassName,
      onClick: onClick,
      canBeActive: format.button.canBeActive,
      isActive: this._buttonStates.get(format.key) === ButtonState.Active,
      disabled: this._buttonStates.get(format.key) === ButtonState.Disabled,
      title: format.button.title + (format.button.shortcut ? ` (${format.button.shortcut})` : ''),
      label: format.button.label,
      className: format.button.className
    };
  }

  private get _formattingBarButtons(): JSX.Element[] {
    const formatSubmenuButtons: IToolbarButtonProps[] = [
      {
        canBeActive: true,
        title: Strings.FormattingBarNormalTextButtonTitle,
        label: Strings.FormattingBarNormalTextButtonTitle,
        onClick: this._handlePlainText
      },
      this._getFormatButtonProps('h2', this._handleHeading2),
      this._getFormatButtonProps('h3', this._handleHeading3),
      this._getFormatButtonProps('h4', this._handleHeading4),
      this._getFormatButtonProps('blockquote', this._handleQuote)
    ];

    const alignmentSubmenuButtons: IToolbarButtonProps[] = [
      this._getFormatButtonProps('alignLeft', this._handleAlignLeft),
      this._getFormatButtonProps('alignCenter', this._handleAlignCenter),
      this._getFormatButtonProps('alignRight', this._handleAlignRight)
    ];

    const listSubmenuButtons: IToolbarButtonProps[] = [
      this._getFormatButtonProps('bullet', this._handleListBullets),
      this._getFormatButtonProps('list', this._handleListNumbered)
    ];

    const buttons: React.ReactElement<IToolbarButtonProps | IToolbarSubmenuProps>[] = [
      <ToolbarSubmenu
        key='format' buttons={formatSubmenuButtons}
        isOpen={this.state.currentSubmenuOpen === 'format'}
        isWide={true}
        onOpen={() => this.setState({ currentSubmenuOpen: 'format'})}
      />,
      <ToolbarButton {...this._getFormatButtonProps('bold', this._handleBold)} />,
      <ToolbarButton {...this._getFormatButtonProps('italic', this._handleItalic)} />,
      <ToolbarButton {...this._getFormatButtonProps('underline', this._handleUnderline)} />,
      <ToolbarSubmenu
        key='alignment'
        buttons={alignmentSubmenuButtons}
        isOpen={this.state.currentSubmenuOpen === 'alignment'}
        onOpen={() => this.setState({ currentSubmenuOpen: 'alignment' })}
      />,
      <ToolbarSubmenu
        key='list'
        buttons={listSubmenuButtons}
        isOpen={this.state.currentSubmenuOpen === 'list'}
        onOpen={() => this.setState({ currentSubmenuOpen: 'list' })}
      />,
      <ToolbarButton {...this._getFormatButtonProps('link', this._handleLink)} />,
      <ToolbarButton
        key='unlink'
        canBeActive={false}
        className='button_linkRemove_temp'
        fabricIconKey='Link'
        onClick={this._handleUnlink}
        title={Strings.FormattingBarUnlinkButtonTitle}
        disabled={this._buttonStates.get('link') !== ButtonState.Active}
      />,
      <ToolbarButton
        key='clearFormatting'
        canBeActive={false}
        fabricIconKey='ClearFormatting'
        onClick={this._handleClearFormatting}
        title={Strings.FormattingBarClearFormattingButtonTitle}
      />
    ];

    return buttons;
  }

  private _finalizeAction(ariaActionName?: string): void {
    if (ariaActionName) {
      this._ariaConfirmAction(ariaActionName);
    }
    this._updateActiveButtons();
  }

  /**
   * @param format - Name of the format to apply
   */
  private _formatOrPrepare(formatKey: string): void {
    this.focus();

    const format: IRichTextFormat = this._config.formats.get(formatKey);
    // Check if the format is masked and skip action
    if (format.maskedBy) {
      for (const maskingFormat of format.maskedBy) {
        if (this._buttonStates.get(maskingFormat) === ButtonState.Active) {
          return;
        }
      }
    }

    const range: IRange = this._currentSelection;
    const [start, end]: number[] = [range.start, range.end];

    const value: string | boolean =
      format.editorValue ? format.editorValue : !this._editor.isFormatAppliedToRange(start, end, format);

    this._cleanupBeforeFormatting(start, end, format);
    const formats: Object = {};
    formats[format.editorKey] = value;
    this._editor.format(start, end, formats);
    this._finalizeAction(formatKey);
  }

  private _handleAlignCenter(): void {
    this._formatOrPrepare('alignCenter');
  }

  private _handleAlignLeft(): void {
    this._formatOrPrepare('alignLeft');
  }

  private _handleAlignRight(): void {
    this._formatOrPrepare('alignRight');
  }

  private _handleBold(): void {
    this._formatOrPrepare('bold');
  }

  private _handleHeading2(): void {
    this._formatOrPrepare('h2');
  }

  private _handleHeading3(): void {
    this._formatOrPrepare('h3');
  }

  private _handleHeading4(): void {
    this._formatOrPrepare('h4');
  }

  private _handleItalic(): void {
    this._formatOrPrepare('italic');
  }

  private _handleLink(): void {
    if (this._currentSelection) {
      // If there is existing link modify its URL, otherwise create new one if selection is not collapsed
      if (this._buttonStates.get('link') === ButtonState.Active) {
        const linkAddress: string =
          this._editor.getFormatValue(
            this._currentSelection.start,
            this._currentSelection.end,
            this._config.formats.get('link')
          ) as string;
        this._openLinkDialog(linkAddress);
      } else if (this._currentSelection.start !== this._currentSelection.end) {
        this._openLinkDialog();
      }
      this._updateActiveButtons();
    }
  }

  private _handleListBullets(): void {
    this._formatOrPrepare('bullet');
  }

  private _handleListNumbered(): void {
    this._formatOrPrepare('list');
  }

  private _handlePlainText(): void {
    this.focus();
    if (this._currentSelection) {
      // Use the clean method for any styling format
      const blockStyles: IRichTextFormat[] =
        this._config.supportedFormatsArray.filter((format) => { return format.isBlockStyle; });
      this._cleanupBeforeFormatting(this._currentSelection.start, this._currentSelection.end, blockStyles[0]);
      this._finalizeAction();
    }
  }

  private _handleQuote(): void {
    this._formatOrPrepare('blockquote');
  }

  /**
    * Checks if a selection change should initiate, then:
    * - If the selection was marked for completion, completes it
    * - Otherwise, markes the selection as initiated to be completed later
    */
  private _handleSelectionChange(range: IRange): void {
    if (range) {
      this.setState({currentSubmenuOpen: undefined});
      if (this._restoreOnFocus()) {
        this._updateFormattingBar();
        return;
      }
    }

    if (!range) {
      this._updateFormattingBar(true);
    } else if (!this._currentSelection || this.state.isFormattingBarHidden) {
      this._currentSelection = range;
      this._updateFormattingBar();
    } else if (this._currentSelection.start !== range.start || this._currentSelection.end !== range.end) {
      // Initiate a selection and complete it if it was marked for completion
      this._selectionInitiated = true;
      if (this._selectionShouldEnd) {
        this._currentSelection = range;
        this._updateFormattingBar();
        // Reset initiation flag because selection change is already handled
        this._selectionInitiated = false;
      }
      // Clear completion flag for future events
      this._selectionShouldEnd = false;
    }
  }

  private _handleClearFormatting(): void {
    this.focus();
    const range: IRange = this._currentSelection;
    const [start, end]: number[] = [range.start, range.end];

    const formats: Object = {};
    for (const format of this._config.supportedFormatsArray) {
      formats[format.editorKey] = false;
    }
    this._editor.format(start, end, formats);
    this._finalizeAction(Strings.FormattingBarClearFormattingButtonTitle);
  }

  private _handleTextChange(): void {
    if (this.state.displayMode === DisplayMode.Edit) {
      // Note: Use if-else because classList.toggle doesn't work properly in IE
      if (this._editorElement.textContent === '') {
        this._editorElement.classList.add('ql-container--blank');
      } else {
        this._editorElement.classList.remove('ql-container--blank');
      }

      if (this._editor) {
        this._currentSelection = this._editor.getSelection() || this._currentSelection;
      }

      if (this.props.onChange) {
        this.props.onChange();
      }
    }
  }

  private _handleUnderline(): void {
    this._formatOrPrepare('underline');
  }

  private _handleUnlink(): void {
    this.focus();
    if (this._currentSelection) {
      this._editor.format(this._currentSelection.start, this._currentSelection.end, {'link': false});
      this._finalizeAction(Strings.FormattingBarUnlinkButtonTitle);
    }
  }

  /**
   * Clean up the existing formatting of given range before applying the new format, by doing:
   *  - Clear all styling formats if new format is also a styling format
   *  - Clear all the formats masked by the new format
   *
   * @param start - Start of the range getting formatted
   * @param end - End of the range getting formatted
   * @param newFormat - Name of new format being applied
   */
  private _cleanupBeforeFormatting(start: number, end: number, newFormat: IRichTextFormat): void {
    const formatsToClear: Object = {};

    // If this is a styling format, clear all styling formats
    if (newFormat.isBlockStyle) {
      const blockStyles: IRichTextFormat[] =
        this._config.supportedFormatsArray.filter((format) => { return format.isBlockStyle; });
      for (const format of blockStyles) {
        formatsToClear[format.editorKey] = false;
      }
    }

    // Clear all formats masked by this format
    if (newFormat.masks) {
      for (const maskedFormatKey of newFormat.masks) {
        formatsToClear[this._config.formats.get(maskedFormatKey).editorKey] = false;
      }
    }

    this._editor.format(start, end, formatsToClear, true);
  }

  private _onLinkDialogSaved(): void {
    if (this._linkDialog.textFieldValue) {
      this._editor.format(this._currentSelection.start, this._currentSelection.end,
        {'link': this._linkDialog.textFieldValue});
    }
    this._linkElement = undefined;
    this._finalizeAction(Strings.FormattingBarLinkButtonTitle);
    this._closeLinkDialog();
  }

  private _openLinkDialog(defaultAddress: string = ''): void {
    this._linkAddress = defaultAddress;

    this.setState({
      isLinkDialogOpen: true
    });
  }

  private _handleEditorFocus(evt: FocusEvent): void {
    this._restoreOnFocus();
  }

  private _handleFormattingBarKeyDown(evt: KeyboardEvent): void {
    if (isEscape(evt)) {
      if (this._editableDiv && !this._editableDiv.isEqualNode(document.activeElement)) {
        this.focus();
        evt.stopPropagation();
        evt.preventDefault();
      }
    }
  }

  private _handleKeyDown(evt: KeyboardEvent): void {
    if (isKey(KeyCodes.F10, evt, true) && this._formattingbarElement) {
      this.context.store.a11yManager.navigateInside(this._formattingbarElement);
      this.context.store.a11yManager.
        ariaAlert(`${Strings.ToolbarNavigationArrowKeys} ${Strings.ToolbarNavigationTabKeys}`);
      evt.stopPropagation();
      evt.preventDefault();
    } else if (isKey('K'.charCodeAt(0), evt, false, true)) {
      this._handleLink();
      evt.stopPropagation();
      evt.preventDefault();
    }
  }

  private _handleKeyUp(evt: KeyboardEvent): void {
    this._checkSelectionChange();
    this._updateActiveButtons();
  }

  private _handleScroll(): void {
    if (this.state.formattingBarPosition) {
      const isSticky: boolean = this.state.formattingBarPosition.position === 'fixed';

      if (this._shouldFormattingBarStick() ? !isSticky : isSticky) {
        const newState: ISPRteState = {
          formattingBarPosition: this._getFormattingBarPosition(),
          isLinkDialogOpen: false
        };
        this.setState(newState);
      }
    }
  }

  private _restoreOnFocus(): boolean {
    let restore: boolean = false;

    if (this._restoreSelectionOnNextFocus && this._currentSelection) {
      this._restoreSelectionOnNextFocus = false;
      this._editor.setSelection(this._currentSelection);
      restore = true;
    }

    if (this._restoreScrollTopOnNextFocus && this._currentScrollTop) {
      this._restoreScrollTopOnNextFocus = false;
      this.context.store.scrollableParent.scrollTop = this._currentScrollTop;
      restore = true;
    }

    return restore;
  }

  private _setButtonStates(): void {
    const range: IRange = this._currentSelection;
    if (range) {
      /* Make a map to keep track of updated buttons. Its goal is to minimize the number
      of calls to isFormatAppliedToRange (equal to the number of currently non-masked formats) */
      const updatedFormats: Map<string, boolean> = new Map<string, boolean>();
      for (const format of this._config.supportedFormatsArray) {
        updatedFormats.set(format.key, false);
      }

      // First figure out the state of masking formats and their masked formats
      const maskingFormats: IRichTextFormat[] =
        this._config.supportedFormatsArray.filter((format) => { return !!format.masks; });
      for (const maskingFormat of maskingFormats) {
        if (!updatedFormats.get(maskingFormat.key)) {
          const isActive: boolean = this._editor.isFormatAppliedToRange(range.start, range.end, maskingFormat);
          this._buttonStates.set(maskingFormat.key, isActive ? ButtonState.Active : ButtonState.Normal);
          updatedFormats.set(maskingFormat.key, true);
          if (isActive) {
            for (const maskedFormat of maskingFormat.masks){
              this._buttonStates.set(maskedFormat, ButtonState.Disabled);
              updatedFormats.set(maskedFormat, true);
            }
          }
        }
      }

      // Figure out activeness for the rest of the formats
      for (const format of this._config.supportedFormatsArray) {
        if (!updatedFormats.get(format.key)) {
          const isActive: boolean = this._editor.isFormatAppliedToRange(range.start, range.end, format);
          this._buttonStates.set(format.key, isActive ? ButtonState.Active : ButtonState.Normal);
        }
      }
    }
  }

  /**
   * This should be called whenever the activeness of buttons could have changed
   * It rerenders the component to make sure the change is rendered
   */
  private _updateActiveButtons(): void {
    this.forceUpdate();
  }

  /**
   * Hide/Show the formatting bar and update the formatting bar position if needed
   * If the editor is not focused, hide the formatting bar
   * If the selection is collapsed the formatting bar will render in a static location
   * If the selection is not collapsed the formatting bar will appear near the selection
   */
  private _updateFormattingBar(isHidden: boolean = false): void {
    if (!this._editorElement) {
      return;
    }

    let updatePosition: boolean = true;

    const newState: ISPRteState = {
      formattingBarPosition: {
        left: SPRte._TOOLBAR_DEFAULT_POSITION.left,
        top: SPRte._TOOLBAR_DEFAULT_POSITION.top,
        position: SPRte._TOOLBAR_DEFAULT_POSITION.position
      },
      isFormattingBarHidden: isHidden
    };

    if (isHidden && this._formattingbarElement) {
      // If the editor or a toolbar button has the focus, don't hide the toolbar
      if (document.activeElement.isEqualNode(this._editableDiv)) {
        isHidden = false;
      } else {
        const buttonNodes: NodeList =
          this._formattingbarElement.querySelectorAll('.ToolbarButton');

        for (let i: number = 0; i < buttonNodes.length; i++) {
          if (document.activeElement.isEqualNode(buttonNodes[i])) {
            newState.isFormattingBarHidden = false;
            newState.formattingBarPosition = this.state.formattingBarPosition;
            isHidden = false;
            // Don't update the position if isHidden got overriden because a button was clicked
            updatePosition = false;
            break;
          }
        }
      }

      if (isHidden && !(document.activeElement as HTMLDivElement).isContentEditable) {
        // This makes the contenteditable element non-editable
        window.getSelection().removeAllRanges();
      }
    }

    if (!isHidden) {
      newState.isFormattingBarHidden = false;

      if (updatePosition) {
        newState.formattingBarPosition = this._getFormattingBarPosition();
      }
    }

    this.setState(newState);
  }
}



