import ISuiteNavModel from '../models/ISuiteNavModel';
import { ISuiteNavDataSource, SuiteNavSource } from './ISuiteNavDataSource';
import {
  HttpClient,
  _LogSource,
  _TraceLogger
} from '@microsoft/sp-client-base';

export default class SPOSuiteNavDataSource implements ISuiteNavDataSource {
  // @TODO Bug 169685 change to readonly
  private static _logSource: _LogSource = _LogSource.create('SPOSuiteNavDataSource');
  public suiteNavService: SuiteNavSource = SuiteNavSource.SPO;
  private _currentUICultureName: string;
  private _httpClient: HttpClient;
  private _siteRelUrl: string;

  /**
   * Initializes a new instance of the SPO
   * @TODO Task 161973 Add PageContext and set the
   * webPermissionMask/suiteNavData.UserDisplayName/webServerRelativeUrl/currentUICultureName
   */
  constructor(httpClient: HttpClient, currentUICultureName: string, webServerRelativeUrl: string) {
    this._currentUICultureName = currentUICultureName;
    this._httpClient = httpClient;
    this._siteRelUrl = webServerRelativeUrl === '/' ? '' : webServerRelativeUrl;
  }

  // Downloads the SPO version of the SuiteNavData
  public loadData(): Promise<ISuiteNavModel> {
    const url: string =
      this._siteRelUrl
      + '/_layouts/15/online/handlers/SpoSuiteLinks.ashx?Locale='
      + this._currentUICultureName
      + '&v=2&mobilereq=0&msajax=1';

    return this._httpClient.post(url, { })
      .then((response: Response) => {
        if (response.status !== 200 || response.headers.get('SharePointError')) {
          _TraceLogger.logVerbose(SPOSuiteNavDataSource._logSource, 'Failed to retrieve SPO SuiteNavData');
          return Promise.reject('Failed to retrieve SPO SuiteNavData');
        }
        return response.json().then((jsonResponse: ISuiteNavModel) => {
          return jsonResponse;
        });
      });
  }
}



