import { IPropertyPaneLabelProps } from './IPropertyPaneLabel';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Label on the PropertyPane.
 * @param targetProperty - Target property the label is associated to.
 * @param properties - Strongly typed Label properties.
 */
export function PropertyPaneLabel(
  targetProperty: string,
  properties: IPropertyPaneLabelProps): IPropertyPaneField<IPropertyPaneLabelProps> {

  return {
    type: IPropertyPaneFieldType.Label,
    targetProperty: targetProperty,
    properties: properties
  };
}



