import * as React from 'react';
import { FocusZone, FocusZoneDirection } from 'office-ui-fabric-react/lib/FocusZone';
import { DisplayMode } from '@microsoft/sp-client-base';
import { IClientSideWebPartManifest } from '@microsoft/sp-module-interfaces';
import { SPFlight } from '@microsoft/sp-client-base';
import { StringHelper } from '@ms/odsp-utilities-bundle';

import CanvasControlType from '../../../canvas/canvas/CanvasControlType';
import { ICanvasContext } from '../../canvas/canvasComponent/ICanvasComponent';
import { IToolboxGroupProps } from '../toolboxGroup/IToolboxGroup';
import { IToolboxItemProps } from '../toolboxItem/IToolboxItem';
import { IToolboxProps, IToolboxState } from './IToolbox';
import ToolboxGroup from '../toolboxGroup/ToolboxGroup';
import ToolboxItem from '../toolboxItem/ToolboxItem';
import ToolboxItemClassifier from '../toolboxItem/ToolboxItemClassifier';
import { IWebPartZoneProps } from '../../../canvas/sampleCanvasControls/WebPartZone';
import {
  isEscape,
  isTab
} from '../../a11y/CanvasA11y';

import './Toolbox.scss';
import Strings from '../../loc/CanvasStrings.resx';

/**
 * @todo [VSO:SPPPlat] #231682
 */
const NEWSFEED_WEBPART_FLIGHT: number = 792;
const NEWSREEL_WEBPART_FLIGHT: number = 790;
const NEWSFEED_ID: string = 'a5df8fdf-b508-4b66-98a6-d83bc2597f63';
const NEWSREEL_ID: string = '0ef418ba-5d19-4ade-9db0-b339873291d0';

export default class Toolbox extends React.Component<IToolboxProps, IToolboxState> {
  public static contextTypes: React.ValidationMap<Toolbox> = {
    store: React.PropTypes.object.isRequired
  };

  public context: ICanvasContext;

  private _domNode: HTMLElement;

  constructor(props: IToolboxProps, context: ICanvasContext) {
    super(props, context);

    this._handleKeyDownCapture = this._handleKeyDownCapture.bind(this);

    this.state = {
      verticalPosition: props.verticalPosition
    };
  }

  public componentDidMount(): void {
    this._domNode.addEventListener('keydown', this._handleKeyDownCapture, true);
  }

  public componentDidUpdate(prevProps: IToolboxProps): void {
    if (this.props.isOpen !== prevProps.isOpen) {
      if (this.props.isOpen) {
        this.context.store.a11yManager.saveState();
        this.context.store.a11yManager.navigateTo(this._domNode);
        // This assumes there is one non-webpart item (RTE)
        const localizedItemCount: string = StringHelper.formatWithLocalizedCountValue(
          Strings.CanvasItems,
          Strings.CanvasItemsInterval,
          this.props.toolboxItems.length + 1
        );
        const message: string =
          StringHelper.format(Strings.ToolboxNavigationArrowKeys, localizedItemCount);
        this.context.store.a11yManager.ariaAlert(message);
      } else {
        this.context.store.a11yManager.clearState();
      }
    }
  }

  public componentWillReceiveProps(nextProps: IToolboxProps): void {
    if (nextProps.isOpen && this.context.store.scrollableParent) {
      // Use the scrollableParent as our viewport
      const {top, bottom}: ClientRect = this.context.store.scrollableParent.getBoundingClientRect();
      const toolboxRowCount: number = Math.min(Math.ceil(nextProps.toolboxItems.length + 1 / 3), 3);
      const toolboxHeight: number = toolboxRowCount * 110;
      const canvasTop: number = this.context.store.canvasElement.getBoundingClientRect().top;
      const hintOffset: number = 43 - (toolboxRowCount * 5);
      let verticalPosition: number = nextProps.verticalPosition;

      // Render Toolbox above hint if there is no room below the hint and there is room above
      if (
        (verticalPosition + toolboxHeight + hintOffset + canvasTop > bottom) &&
        (verticalPosition - toolboxHeight - hintOffset > top)
        ) {
        verticalPosition -= toolboxHeight + hintOffset;
      }

      this.setState({ verticalPosition });
    }
  }

  public render(): React.ReactElement<IToolboxProps> {
    const { isOpen, onItemClick, toolboxItems }: IToolboxProps = this.props;
    const classifier: ToolboxItemClassifier = new ToolboxItemClassifier();

    for (const item of toolboxItems) {
      /* tslint:disable:no-any */
      const webPartManifest: IClientSideWebPartManifest<any> = (item.itemProps as IWebPartZoneProps).webPartManifest;
      /* tslint:enable:no-any */

      /**
       * Component manifests will be sent to the client regardless of flight status.
       * Currently checking the newsreel flight on client to hide the web part from the Toolbox
       * if the flight is not on. @todo [VSO:SPPPlat] #231682
       */
      if ((webPartManifest.id === NEWSREEL_ID && !SPFlight.isEnabled(NEWSREEL_WEBPART_FLIGHT)) ||
          (webPartManifest.id === NEWSFEED_ID && !SPFlight.isEnabled(NEWSFEED_WEBPART_FLIGHT))) {
          continue;
      }

      item.key = webPartManifest.id;

      (item as IToolboxItemProps).onClick = onItemClick;
      item.msIconName = webPartManifest.preconfiguredEntries[0].officeFabricIconFontName;
      classifier.registerItem(webPartManifest.id, item as IToolboxItemProps);
    }

    // Construct the featured toolbox group
    const featuredItemViews: React.ReactElement<IToolboxItemProps>[] = [];

    // First add the RTE to featured items
    featuredItemViews.push(
      <ToolboxItem
        description={Strings.TextWebpartDescription}
        displayName={Strings.TextWebPartDisplayName}
        itemProps={{controlType: CanvasControlType.RTE, displayMode: DisplayMode.Edit, id: undefined}}
        msIconName='Font'
        onClick={onItemClick} />
    );

    // next get the featured items and add them to the toolbox
    const featuredItemProps: Array<IToolboxItemProps> = classifier.classify(true);
    featuredItemProps.forEach((item: IToolboxItemProps) => {
      featuredItemViews.push(<ToolboxItem {...(item as IToolboxItemProps)}/>);
    });

    // Add them to the featured group
    const featuredGroup: React.ReactElement<IToolboxGroupProps> =
      <ToolboxGroup
        groupName={Strings.ToolboxGroupNameFeatured} key={Strings.ToolboxGroupNameFeatured}
        hasHeader={true}>
        { featuredItemViews }
      </ToolboxGroup>;

    // Construct the non featured toolbox group
    const nonFeaturedItemViews: React.ReactElement<IToolboxItemProps>[] = [];
    const nonFeaturedItemProps: Array<IToolboxItemProps> = classifier.classify(
      false,
      (item1: IToolboxItemProps, item2: IToolboxItemProps) => {
        return item1.displayName.localeCompare(item2.displayName);
      });

    nonFeaturedItemProps.forEach((item: IToolboxItemProps) => {
        nonFeaturedItemViews.push(<ToolboxItem {...(item as IToolboxItemProps)}/>);
    });

    // Add them to the non featured group
    const nonFeaturedGroup: React.ReactElement<IToolboxGroupProps> =
      <ToolboxGroup
        groupName={Strings.ToolboxGroupNameNonFeatured} key={Strings.ToolboxGroupNameNonFeatured}
        hasHeader={false}>
        { nonFeaturedItemViews }
      </ToolboxGroup>;

    return (
      <div
        role='toolbar'
        className='CanvasToolbox'
        ref={(ref) => this._domNode = ref}
        style={{display: isOpen ? 'block' : 'none', top: this.state.verticalPosition}}>
        <FocusZone direction={FocusZoneDirection.horizontal} isCircularNavigation={true}>
          { featuredGroup }
          { nonFeaturedGroup }
        </FocusZone>
      </div>
    );
  }

  private _handleKeyDownCapture(e: KeyboardEvent): void {
    if (isEscape(e)) {
      this.context.store.a11yManager.restoreState();
      this.context.store.closeToolbox();
      e.stopPropagation();
      e.preventDefault();
    } else if (isTab(e)) {
      e.stopPropagation();
      e.preventDefault();
    }
  }
}


