/**
 * @copyright Microsoft Corporation. All rights reserved.
 *
 * @file The button component used by toolbars on the Canvas.
 * It implements all common functionalities expected of a toolbar button.
 * It is used in the Canvas control toolbar and Rich Text Editor formatting bar.
 */

import { css } from 'office-ui-fabric-react/lib/utilities/css';
import * as React from 'react';
import { IToolbarButton } from './IToolbarButton';

import './ToolbarButton.scss';

export enum ButtonState {
  Active,
  Disabled,
  Normal
}

/**
 * Interface of ToolbarButton.
 * The toolbar needs to provide the fields for its buttons
 */
export interface IToolbarButtonProps extends IToolbarButton {
  /**
   * Button onClick callback
   */
  onClick: () => void;

  /**
   * If the button should be rendered in disabled state
   */
  disabled?: boolean;

  /**
   * If the button should be rendered in active state
   */
  isActive?: boolean;

  /**
   * Unique string used to identify the button inside the toolbar
   */
  key?: string;

  tabIndex?: number;
}

/**
 * @class The button component used by toolbars on the Canvas.
 */
export default class ToolbarButton extends React.Component<IToolbarButtonProps, void> {
  /* tslint:disable:member-access */
  static defaultProps: IToolbarButtonProps = {
    // required fields default to undefined
    key: undefined,
    fabricIconKey: undefined,
    onClick: undefined,

    // optional fields default values
    canBeActive: true,
    className: 'CanvasControlToolbar-item',
    disabled: false,
    isActive: false
  };

  /**
   * Constructs an instance of ToolbarButton
   */
  constructor(props: IToolbarButtonProps) {
    super(props);
  }

  /**
   * Renders ToolbarButton
   */
  public render(): React.ReactElement<IToolbarButtonProps> {
    const buttonClassName: string = css(
      'ToolbarButton',
      this.props.className,
      {
        'ToolbarButton--disabled': this.props.disabled,
        'ToolbarButton--active': this.props.isActive
      }
    );

    // @todo 198107: Clean this up when mdl2 icons are in
    const iconCssClass: string = this.props.fabricIconKey ?
      `ms-Icon ms-Icon--${this.props.fabricIconKey}` : (this.props.iconClassName || '');

    return (
      <button
        aria-disabled={this.props.disabled ? true : undefined}
        aria-label={this.props.title}
        aria-pressed={this.props.canBeActive ? this.props.isActive : undefined}
        className={buttonClassName}
        key={this.props.key}
        onClick={this.props.onClick}
        title={this.props.title}
        tabIndex={this.props.tabIndex}
      >
        <i
          className={iconCssClass}
        />
        { this.props.label }
      </button>
    );
  }
}



