import { IPropertyPaneDropdownProps } from './IPropertyPaneDropdown';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Dropdown on the PropertyPane.
 * @param targetProperty - Target property the dropdown is associated to.
 * @param properties - Strongly typed Dropdown properties.
 */
export function PropertyPaneDropdown(
  targetProperty: string,
  properties: IPropertyPaneDropdownProps): IPropertyPaneField<IPropertyPaneDropdownProps> {

  return {
    type: IPropertyPaneFieldType.Dropdown,
    targetProperty: targetProperty,
    properties: properties
  };
}


