import {
  BasicHttpClient,
  HttpClient,
  IServiceScopeProvider,
  _LogSource,
  ServiceScope,
  _TraceLogger,
  basicHttpClientServiceKey,
  httpClientServiceKey,
  _PerformanceLogger,
  PageContext,
  pageContextServiceKey,
  Environment,
  EnvironmentType,
  environmentServiceKey
} from '@microsoft/sp-client-base';

import ClientSideApplication from './ClientSideApplication';
import IComponentVisible from '../../common/spPageChrome/IComponentVisible';
import SuiteNavManager from '../../common/suiteNav/SuiteNavManager';
import ISPPageChrome from '../../common/spPageChrome/ISPPageChrome';
import SPPageChrome from '../../common/spPageChrome/SPPageChrome';
import PreloadedDataReader, { IPreloadedData } from './PreloadedDataReader';
import AppPageContext, { appPageContextServiceKey } from '../pageContext/AppPageContext';

/**
 * The system shell manages the initial loading of the scripts for a client-side application,
 * as well as constructin the ClientSideApplication object, and initializing key services such
 * as the page chrome, page context, HTTP client, etc.
 *
 * @alpha
 */
export interface IShell extends IServiceScopeProvider {
  /**
   * Describes the type of environment that the framework is being hosted on.
   */
  environment: Environment;

  /**
   * Returns the current page context object for webparts.
   */
  pageContext: PageContext;

  /**
   * Returns the current page context object for the applcation.
   * Will be phased out soon.
   */
  appPageContext: AppPageContext;

  /**
   * BasicHttpClient is used to perform REST calls with general internet services.
   * For communicating with SharePoint services, use HttpClient instead.
   */
  basicHttpClient: BasicHttpClient;

  /**
   * HttpClient is used to perform REST calls with SharePoint services.
   * It implements special enhancements such as configuring default headers, adding
   * an "X-RequestDigest" header for write operations, and collecting telemetry
   * to help the service monitor the performance and reliability of the application.
   *
   * For communicating with non-SharePoint services, use BasicHttpClient instead.
   */
  httpClient: HttpClient;

  /**
   * This object allows the application to manage the suite navigation of
   * the standard page chrome.  For more information about Suite Navigation,
   * see this article:
   * https://msdn.microsoft.com/en-us/library/office/dn614990.aspx
   */
  suiteNav: IComponentVisible;
}

export interface IClientSideApplicationWrapper {
  default: ClientSideApplication;
}

/**
 * RESERVED FOR INTERNAL USAGE.  Applications interact with the shell via
 * the IShell interface.
 *
 * @internal
 */
export default class Shell implements IShell {
  private static _logSource: _LogSource = _LogSource.create('Shell');
  private static _instance: IShell = undefined;

  private _rootServiceScope: ServiceScope;
  private _spPageChrome: ISPPageChrome;
  private _application: ClientSideApplication;
  private _suiteNav: SuiteNavManager;
  private _preloadedDataReader: PreloadedDataReader;
  private _appPageContext: AppPageContext;
  private _pageContext: PageContext;
  private _basicHttpClient: BasicHttpClient;
  private _httpClient: HttpClient;
  private _environment: Environment;

  public static getInstance(): IShell {
    if (Shell._instance === undefined) {
      Shell._instance = new Shell();
    }
    return Shell._instance;
  }

  public get serviceScope(): ServiceScope {
    return this._rootServiceScope;
  }

  public get pageContext(): PageContext {
    return this._pageContext;
  }

  public get appPageContext(): AppPageContext {
    return this._appPageContext;
  }

  public get basicHttpClient(): BasicHttpClient {
    return this._basicHttpClient;
  }

  public get httpClient(): HttpClient {
    return this._httpClient;
  }

  public get suiteNav(): IComponentVisible {
    return this._suiteNav;
  }

  public get environment(): Environment {
    return this._environment;
  }

  public start(module: IClientSideApplicationWrapper | ClientSideApplication,
                     preloadedData: IPreloadedData): void {
    this._initializePreload(preloadedData);
    _PerformanceLogger.markApplicationStart();
    this._startApplication((module as IClientSideApplicationWrapper).default || module as ClientSideApplication);
  }

  private _startApplication(application: ClientSideApplication): void {
    /* tslint:disable:no-string-literal */
    if (application['__type'] !== 'ClientSideApplication') {
      /* tslint:enable:no-string-literal */
      throw new Error('The application is not a subclass of ClientSideApplication');
    }

    this._application = application;
    this._application.load(this);

    // Checks if the page is being served by localhost, which doesn't provide SuiteNav Services
    // @todo Bug 190509 Implement Mock SuiteNav
    this._suiteNav = new SuiteNavManager(
      this._application.suiteNavConfiguration(),
      this._spPageChrome.suiteNavDiv,
      this.serviceScope);

    this._suiteNav.loadSuiteNav();
    this._application.render(this._spPageChrome.appDiv);
  }

  private _initializePreload(preloadedData: IPreloadedData): void {
    // We encountered IE compatibility errors when trying to access the window variable
    // without using the [] notation
    /* tslint:disable:no-string-literal */
    window['_spClientSidePageContext'] = preloadedData;

    // HACK (VSO#221483): Setting the _spPageContextInfo on the window to workaround Manifest v2 issues
    window['_spPageContextInfo'] = preloadedData.spPageContextInfo;
    /* tslint:enable:no-string-literal */

    _TraceLogger.logVerbose(Shell._logSource, 'Starting sp-client-preview');

    this._rootServiceScope = ServiceScope.startNewRoot();
    this._environment = this._rootServiceScope
      .createDefaultAndProvide(environmentServiceKey) as Environment;
    this._pageContext = this._rootServiceScope
      .createDefaultAndProvide(pageContextServiceKey) as PageContext;
    this._appPageContext = this._rootServiceScope
      .createDefaultAndProvide(appPageContextServiceKey) as AppPageContext;
    this._basicHttpClient = this._rootServiceScope.createAndProvide(basicHttpClientServiceKey,
      BasicHttpClient);
    this._httpClient = this._rootServiceScope.createAndProvide(httpClientServiceKey, HttpClient);
    this._rootServiceScope.finish();

    this._preloadedDataReader = new PreloadedDataReader(preloadedData);

    this._initializeEnvironment(this._environment);
    this._preloadedDataReader.initializeDigestCache(this._rootServiceScope);
    this._preloadedDataReader.initializePageContext(this._appPageContext);
    this._initializePageContextFromAppContext(this._pageContext, this._appPageContext);
    this._spPageChrome = SPPageChrome.getInstance(this._rootServiceScope);
  }

  /**
   * Populates the webPart pageContext using the data from the app Page Context. AppPageContext
   * must be populated already.
   */
  private _initializePageContextFromAppContext(pageContext: PageContext, appPageContext: AppPageContext): void {
    pageContext.initialize({
      cultureInfo: {
        currentCultureName: appPageContext.core.currentCultureName,
        currentUICultureName: appPageContext.core.currentUICultureName
      },
      site: {
        id: appPageContext.core.siteId
      },
      user: {
        displayName: appPageContext.core.userDisplayName,
        loginName: appPageContext.core.userLoginName
      },
      web: {
        absoluteUrl: appPageContext.core.webAbsoluteUrl,
        id: appPageContext.core.webId,
        serverRelativeUrl: appPageContext.core.webServerRelativeUrl,
        title: appPageContext.core.webTitle
      }
    });
  }

  private _initializeEnvironment(environment: Environment): void {
    let type: EnvironmentType;
    /* tslint:disable:no-string-literal */
    if (
      window.location.hostname === 'localhost' ||
      window['ENVIRONMENTTYPE'] &&
      window['ENVIRONMENTTYPE'] === 'Local') {
    /* tslint:enable:no-string-literal */
      type = EnvironmentType.Local;
    } else {
      type = EnvironmentType.SharePoint;
    }
    environment._initialize(type);
  }
}




