import {
  ServiceScope,
  Environment,
  environmentServiceKey
} from '@microsoft/sp-client-base';

import IWebPartHost from '../../../../common/webParts/IWebPartHost';
import { WebPartConfigurationEvent } from '../../../../common/webParts/PropertyPaneDefinitions';

/**
 * CanvasWebPartHost is the IWebPartHost implementation used by the Canvas. Each Canvas
 * will instantiate their own CanvasWebPartHost, which means the web parts are scoped by
 * Canvas.
 */
export default class CanvasWebPartHost implements IWebPartHost {
  private _handleCanvasChanged: () => void;
  private _handleResize: (element: HTMLElement, duration: number, margin?: number) => void;
  private _reclaimFocus: () => void;
  private _serviceScope: ServiceScope;
  private _environment: Environment;

  /**
   * @param handleCanvasChanged - Optional. Callback invoked when web parts are interacted with.
   */
  constructor(
    serviceScope: ServiceScope,
    handleResize: (element: HTMLElement, duration: number, margin?: number) => void,
    handleCanvasChanged?: () => void,
    reclaimFocus?: () => void) {
    this._handleCanvasChanged = handleCanvasChanged;
    this._handleResize = handleResize;
    this._reclaimFocus = reclaimFocus;
    this._serviceScope = serviceScope;

    this.webPartConfigurationEventCallback = this.webPartConfigurationEventCallback.bind(this);

    serviceScope.whenFinished(() => {
      this._environment = serviceScope.consume(environmentServiceKey) as Environment;
    });
  }

  /**
   * @see IWebPartHost
   */
  public get serviceScope(): ServiceScope {
    return this._serviceScope;
  }

  /**
   * @see IWebPartHost
   */
  public setDirty(wpInstanceId: string): void {
    if (this._handleCanvasChanged) {
      this._handleCanvasChanged();
    }
  }

  /**
   * @see IWebPartHost
   */
  public webPartConfigurationEventCallback(event: WebPartConfigurationEvent): void {
    if (event === WebPartConfigurationEvent.OpenComplete) {
      /*
       * Scroll web part back into with a 500ms animation and
       * 44px additional margin (2 * the _VERTICAL_MARGIN property in CanvasZone)
       */
      this._handleResize(document.querySelector('.CanvasZone--selected') as HTMLElement, 500, 44);
    } else if (event === WebPartConfigurationEvent.CloseComplete || event === WebPartConfigurationEvent.LostFocus) {
      this._reclaimFocus();
    }
  }
}



