import { Validate, _ILogSource, _TraceLogger } from '@microsoft/sp-client-base';

/**
 * Execute the passed in delegate with a guarantee that no exception will leak out.
 * Do not use this normally. Only in error handling scenarios where you would like
 * to be defesive.
 * @param {method} method to execute
 * @apram {logSource} log source
 */
/* tslint:disable:export-name */
export function executeWithoutFailing(method: () => void, logSource: _ILogSource): void {
  /* tslint:enable:export-name */
  Validate.isNotNullOrUndefined(method, 'method');

  try {
    method();
  } catch (e) {
    // Defensive code as this method should never ever throw
    if (logSource) {
      _TraceLogger.logError(logSource, new Error('Error: ' + e));
    }
  }
}


