/** ReservedEventNames
 *  Event names should follow the following format
 *  'EventName:Action'
 *
 *  e.g.
 *    'configure:start'
 *    'configure:end'
 *
 * @alpha
 */
/* tslint:disable:no-internal-module */
module ReservedEventNames {
/* tslint:enable:no-internal-module */
    'use strict';

    export const delimitor: string = ':';

    /* Commonly used events */
    export const configure: string = 'configure';

    /* Commonly used acitons a.k.a. event verbs */
    export const start: string = 'start';
    export const end: string = 'end';

    /* Commonly used event names */
    export const configureStart: string = configure + delimitor + start;
    export const configureEnd: string = configure + delimitor + end;

    /* Event source */
    export const classicToolpane: string = 'ClassicToolpane';
    export const classicToolpaneId: string = '121ff297-293c-4d66-a24b-916156760cab';
}

export default ReservedEventNames;



