import { IPropertyPaneCheckboxProps } from './IPropertyPaneCheckbox';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Checkbox on the PropertyPane.
 * @param targetProperty - Target property the checkbox is associated to.
 * @param properties - Strongly typed Checkbox properties.
 */
export function PropertyPaneCheckbox(
  targetProperty: string,
  properties: IPropertyPaneCheckboxProps): IPropertyPaneField<IPropertyPaneCheckboxProps> {

  return {
    type: IPropertyPaneFieldType.CheckBox,
    targetProperty: targetProperty,
    properties: properties
  };
}


