import ISuiteNavModel from '../models/ISuiteNavModel';
import SuiteNavManager from '../SuiteNavManager';
import { ISuiteNavDataSource, SuiteNavSource } from './ISuiteNavDataSource';
import {
  HttpClient,
  _LogSource,
  _TraceLogger
} from '@microsoft/sp-client-base';

// Interface for the object returned by Response.json()
interface IResponseJSONStringObject {
  'odata.metadata': string;
  value: string;
}

export default class OnPremSuiteNavDataSource implements ISuiteNavDataSource {
  // @TODO Bug 169685 change to readonly
  private static _logSource: _LogSource = _LogSource.create('OnPremSuiteNavDataSource');
  public suiteNavService: SuiteNavSource = SuiteNavSource.OnPrem;
  private _currentUICultureName: string;
  private _httpClient: HttpClient;
  private _siteRelUrl: string;

  /**
   * Initializes a new instance of the SuiteNavManagerConfiguration
   * @TODO Task 161973 Add PageContext and set the
   * webPermissionMask/suiteNavData.UserDisplayName/webServerRelativeUrl/currentUICultureName
   */
  constructor(httpClient: HttpClient, currentUICultureName: string, webServerRelativeUrl: string) {
    this._currentUICultureName = currentUICultureName;
    this._httpClient = httpClient;
    this._siteRelUrl = webServerRelativeUrl === '/' ? '' : webServerRelativeUrl;
  }

  // Downloads the OnPrem version of the SuiteNavData
  public loadData(): Promise<ISuiteNavModel> {
    const url: string =
      this._siteRelUrl
      + '/_api/Microsoft.SharePoint.Portal.SuiteNavData.GetSuiteNavData?v=2&Locale='
      + this._currentUICultureName;

    return this._httpClient.get(url)
      .then((response: Response) => {
        if (response.status !== 200) {
          this._logAndThrowSuiteNavLoadingError();
        }

        return response.json().then((jsonString: IResponseJSONStringObject) => {
          if (jsonString.value === '') {
            this._logAndThrowSuiteNavLoadingError();
          } else {
            const suiteNavResponse: ISuiteNavModel = <ISuiteNavModel>JSON.parse(jsonString.value);
            // If the height of the suiteNav is not provided, assume that it is 50px
            if (!suiteNavResponse.NavBarData.Dimensions) {
              suiteNavResponse.NavBarData.Dimensions = { Top: SuiteNavManager._defaultSuiteNavHeight};
            }
            return suiteNavResponse;
          }
        });
      });
  }

  private _logAndThrowSuiteNavLoadingError(): void {
    _TraceLogger.logVerbose(OnPremSuiteNavDataSource._logSource, 'Failed to retrieve Hybrid SuiteNavData');
    throw new Error('Failed to retrieve Hybrid SuiteNavData');
  }
}



