import * as Combokeys from 'combokeys';
import { ISPModuleLoader } from './SPModuleLoader';
import { IClientSideComponentManifest } from '@microsoft/sp-module-interfaces';

/* tslint:disable:no-any */
let importableSpModuleLoader: ISPModuleLoader = (window as any).spModuleLoader as ISPModuleLoader;
/* tslint:enable:no-any */

// Return a dummy module loader if the module loader isn't present on the page. This occurs during unit tests.
if (!importableSpModuleLoader) {
  importableSpModuleLoader = {
    combokeys: undefined,

    loadScript: (): Promise<void> => Promise.reject(new Error('Module loader not present')),

    loadStandardLibraryModule: (): Promise<void> => Promise.reject(new Error('Module loader not present')),

    loadManifestModules: (): Promise<void> => Promise.reject(new Error('Module loader not present')),

    importManifests: (): void => { throw new Error('Module loader not present'); },

    forceLoadDebugManifests: (): Promise<void> => { throw new Error('Module loader not present'); },

    getManifests: (): IClientSideComponentManifest[] => [],

    loadCss: (): void => { throw new Error('Module loader not present'); }
  };
}

export const combokeys: typeof Combokeys = importableSpModuleLoader ? importableSpModuleLoader.combokeys : undefined;

export default importableSpModuleLoader;



