import * as React from 'react';

import { IToolboxItemProps } from './IToolboxItem';

import './ToolboxItem.scss';

export default function ToolboxItem(props: IToolboxItemProps): React.ReactElement<IToolboxItemProps> {
  return (
    <button
      role='option'
      className='CanvasToolboxItem'
      title={ props.description }
      onClick={() => props.onClick(JSON.parse(JSON.stringify(props.itemProps)))}
    >
      <span className={`CanvasToolboxItem-icon ms-Icon ms-Icon--${props.msIconName}`}/>
        <span className='CanvasToolboxItem-title'>
          { props.displayName }
        </span>
    </button>
   );
}


