import * as React from 'react';
import Strings from '../../sp-canvas/loc/CanvasStrings.resx';

import {
  Dialog,
  DialogType,
  DialogFooter,
  Button,
  ButtonType,
  TextField
} from 'office-ui-fabric-react';

import {
  isEnter
} from '../../sp-canvas/a11y/CanvasA11y';

export interface ISPRteLinkDialogProps {
  editorElement: HTMLElement;
  isOpen: boolean;
  onDismiss: () => void;
  onSaveClicked: () => void;
  onCancelClicked: () => void;
  textFieldValue: string;
  validationRegex: RegExp;
}

export interface ISPRteLinkDialogState {
  isURLValid: boolean;
}

export default class SPRteLinkDialog extends React.Component<ISPRteLinkDialogProps, ISPRteLinkDialogState> {
  private _initCursorSet: boolean;
  private _textField: TextField;

  public constructor(props: ISPRteLinkDialogProps) {
    super(props);

    this._getErrorMessage = this._getErrorMessage.bind(this);
    this._handleKeyDown = this._handleKeyDown.bind(this);
    this._handleTextFieldChange = this._handleTextFieldChange.bind(this);
    this._isURLValid = this._isURLValid.bind(this);

    this.state = {
      isURLValid: true
   };
 }

  public render():  React.ReactElement<ISPRteLinkDialogProps> {
    const {
      editorElement,
      isOpen,
      onDismiss,
      onSaveClicked,
      onCancelClicked,
      textFieldValue
   }: ISPRteLinkDialogProps = this.props;

    return (
      <Dialog
        isOpen={isOpen}
        type={DialogType.normal}
        onDismiss={onDismiss}
        title={Strings.LinkDialogTitle}
        isBlocking={true}
        elementToFocusOnDismiss={editorElement}
      >
        <TextField
          className='SPRteLinkDialog-textField'
          label={Strings.LinkDialogTextFieldLabel}
          ariaLabel={Strings.LinkDialogTextFieldAriaLabel}
          onChanged={this._handleTextFieldChange}
          onGetErrorMessage={this._getErrorMessage}
          onKeyDown={this._handleKeyDown}
          defaultValue={textFieldValue}
          ref={(c: TextField) => this._textField = c}
        />
        <DialogFooter>
          <Button
            buttonType={ButtonType.primary}
            disabled={!this.state.isURLValid}
            onClick={onSaveClicked}
            ariaLabel={Strings.RichTextLinkDialogSaveButtonLabel}>
              {Strings.RichTextLinkDialogSaveButtonLabel}
            </Button>
          <Button
            onClick={onCancelClicked}
            ariaLabel={Strings.RichTextLinkDialogCancelButtonLabel}>
            {Strings.RichTextLinkDialogCancelButtonLabel}
          </Button>
        </DialogFooter>
      </Dialog>
    );
 }

  public componentDidUpdate(prevProps: ISPRteLinkDialogProps): void {
    if (this._textField && this.props.isOpen && !this._initCursorSet) {
      this._initCursorSet = true;
      this._textField.select();

      const textValue: string = this._textField.value;
      if (textValue) {
        this._textField.setSelectionStart(textValue.length);
        this._textField.setSelectionEnd(textValue.length);
     }
   } else if (!this.props.isOpen) {
     this._initCursorSet = false;
   }
 }

  public componentWillReceiveProps(newProps: ISPRteLinkDialogProps): void {
    this.state = {
      isURLValid: this._isURLValid(newProps.textFieldValue)
   };
 }

  public get textFieldValue(): string {
    let url: string = '';
    if (this._textField) {
      url = this._textField.value;
      if (!this._hasProtocol(url)) {
        url = 'http://' + url;
     }
   }
    return url;
 }

  private _getErrorMessage(): string {
    return !this._isURLValid(this.textFieldValue) && this.textFieldValue !== '' ?
      Strings.LinkDialogErrorNotSupportedLink : '';
 }

  private _handleKeyDown(evt: KeyboardEvent): void {
    if (isEnter(evt) && this.state.isURLValid) {
      this.props.onSaveClicked();
      evt.stopPropagation();
      evt.preventDefault();
    }
  }

  private _handleTextFieldChange(): void {
    this.setState({
      isURLValid: this._isURLValid(this.textFieldValue)
   });
 }

  private _hasProtocol(url: string): boolean {
    const hasProtocolRegex: RegExp = new RegExp('^([A-Za-z0-9]+:(\/\/)?|#|\/|\\\\).+', 'i');
    return hasProtocolRegex.test(url);
 }

  private _isURLValid(url: string): boolean {
    return this.props.validationRegex.test(url);
 }
}



