import {
ServiceScope,
httpClientServiceKey,
HttpClient,
_LogSource,
_TraceLogger
} from '@microsoft/sp-client-base';
import spModuleLoader from '@microsoft/sp-module-loader';
import { DataStore, DataStoreCachingType } from '@ms/odsp-utilities-bundle';

import ISuiteNavManagerConfiguration from './ISuiteNavManagerConfiguration';
import ISuiteNavModel from './models/ISuiteNavModel';
import IO365Shell from './IO365Shell';
import { ISuiteNavDataSource, SuiteNavSource } from './dataAccess/ISuiteNavDataSource';
import SPOSuiteNavDataSource from './dataAccess/SPOSuiteNavDataSource';
import OnPremSuiteNavDataSource from './dataAccess/OnPremSuiteNavDataSource';
import O365ShellWrapper from './O365ShellWrapper';
import SPPageChrome from '../spPageChrome/SPPageChrome';
import IComponentVisible from '../spPageChrome/IComponentVisible';

export default class SuiteNavManager implements IComponentVisible {
  public static _defaultSuiteNavHeight: string = '50px';
  private static CACHE_PREFIX: string = 'SPPPLATSuiteNav';
  private static SERVICE_KEY: string = 'SuiteNavService';
  private static DATA_KEY: string = 'Data';
  private static DATE_KEY: string = 'CacheDate';
  private static VERSION_KEY: string = 'CacheVersion';
  private static SESSION_KEY: string = 'CachedThisSession';
  // This constant can be used to invalidate the cache if we make incompatible changes
  private static CACHE_VERSION: number = 1;
  private static _cache: DataStore = new DataStore(SuiteNavManager.CACHE_PREFIX, DataStoreCachingType.local);
  private static _logSource: _LogSource = _LogSource.create('SuiteNavManager');

  private _o365Shell: IO365Shell;
  private _suiteNavManagerConfiguration: ISuiteNavManagerConfiguration;
  private _httpClient: HttpClient;
  private _suiteNavDataSource: ISuiteNavDataSource;
  private _suiteNavDiv: HTMLDivElement;
  private _suiteNavModel: Promise<ISuiteNavModel>;
  private _serviceScope: ServiceScope;

  private static _updateCache(model: ISuiteNavModel, service: SuiteNavSource): void {
    SuiteNavManager._cache.setValue(SuiteNavManager.DATA_KEY, model);
    SuiteNavManager._cache.setValue(SuiteNavManager.DATE_KEY, new Date());
    SuiteNavManager._cache.setValue(SuiteNavManager.VERSION_KEY, SuiteNavManager.CACHE_VERSION);
    SuiteNavManager._cache.setValue(SuiteNavManager.SESSION_KEY, true, DataStoreCachingType.session);
    SuiteNavManager._cache.setValue(SuiteNavManager.SERVICE_KEY, service);
  }

  private static _clearCache(): void {
    SuiteNavManager._cache.remove(SuiteNavManager.DATA_KEY);
    SuiteNavManager._cache.remove(SuiteNavManager.DATE_KEY);
    SuiteNavManager._cache.remove(SuiteNavManager.VERSION_KEY);
    SuiteNavManager._cache.remove(SuiteNavManager.SERVICE_KEY);
  }

  constructor(
    suiteNavManagerConfiguration: ISuiteNavManagerConfiguration,
    suiteNavDiv: HTMLDivElement,
    serviceScope: ServiceScope) {

    this._suiteNavManagerConfiguration = suiteNavManagerConfiguration;
    this._suiteNavDataSource = undefined;
    this._o365Shell = undefined;
    this._suiteNavManagerConfiguration.o365ShellRenderSettings.top = suiteNavDiv.id;
    this._suiteNavDiv = suiteNavDiv;
    this._serviceScope = serviceScope;

    serviceScope.whenFinished(() => {
      this._httpClient = serviceScope.consume(httpClientServiceKey);
    });

    if (!suiteNavManagerConfiguration.isSuiteNavDisabled()) {
      SPPageChrome.getInstance(this._serviceScope).changeSuiteNavHeight(SuiteNavManager._defaultSuiteNavHeight);
    }
  }

  /**
   * Sets the SuiteNav to be visible or not.
   * @param setVisible Set the visibility to be on or off.
   */
  public setComponentVisibility(setVisible: boolean): void {
    this._suiteNavDiv.hidden = !setVisible;
    if (this._suiteNavModel) {
      this._suiteNavModel.then((model: ISuiteNavModel) => {
        SPPageChrome.getInstance(this._serviceScope).changeSuiteNavHeight(
          setVisible ?
            model.NavBarData.Dimensions.Top :
            '0px');
      });
    }
  }

  /**
   * Loads the SuiteNav into the page by obtaining the data, building the model, getting the shell wrapper
   * and rendering it onto the page.
   */
  public loadSuiteNav(): void {
    if (!this._suiteNavManagerConfiguration.isSuiteNavDisabled()) {
      this._suiteNavModel = this._getSuiteNavModel();
      this._suiteNavModel
        .then(
        (suiteNavModel: ISuiteNavModel) => this._modifySuiteNavModel(suiteNavModel))
        .then(
        (suiteNavModel: ISuiteNavModel) => this._getSuiteNavShell(suiteNavModel))
        .then(
        (o365ShellWrapper: IO365Shell) => this._renderSuiteNav(o365ShellWrapper));
    }
  }

  /**
   * Gets the suite nav data. Tries to grab SPO and OnPrem version in parallel and if SPO fails then
   * the onPrem version is loaded.
   * @param webServerRelativeUrl represents the SharePoint web server relative URL
   * @param cultureName represents the name of the culture (e.g. 'en-us')
   * @returns a promise that resolves to a string containing the response from the Suite Nav service.
   */
  private _getSuiteNavModel(): Promise<ISuiteNavModel> {
    if (this._isCacheValid() && !this._isCacheOutdated()) {
      return this._loadSuiteNavFromCache();
    } else {
      SuiteNavManager._clearCache();
      return this._loadSuiteNavFromServer();
    }
  }

  private _loadSuiteNavFromCache(): Promise<ISuiteNavModel> {
    return new Promise<ISuiteNavModel>((complete?: (result: ISuiteNavModel) => void) => {
      SuiteNavManager._cache.getValue<SuiteNavSource>(SuiteNavManager.SERVICE_KEY) ===
        SuiteNavSource.SPO ?
        this._suiteNavDataSource = this._createSPODataSource() :
        this._suiteNavDataSource = this._createOnPremDataSource();
      complete(SuiteNavManager._cache.getValue<ISuiteNavModel>(SuiteNavManager.DATA_KEY));
    });
  }

  private _loadSuiteNavFromServer(): Promise<ISuiteNavModel> {
    this._suiteNavDataSource = this._createSPODataSource();

    const onPremDataSource: OnPremSuiteNavDataSource = this._createOnPremDataSource();

    const onPremSuiteNavResponse: Promise<ISuiteNavModel> = onPremDataSource.loadData();
    onPremSuiteNavResponse.catch(() => {
      this._createSuiteNavErrorLogEntry('loadData', 'Failed to retrieve Hybrid SuiteNavData');
    });

    return this._suiteNavDataSource.loadData().then((response: ISuiteNavModel) => {
      return response;
    }).catch(() => {
      this._createSuiteNavErrorLogEntry('loadData', 'Failed to retrieve SPO SuiteNavData');
      this._suiteNavDataSource = onPremDataSource;
      return onPremSuiteNavResponse;
    });
  }

  /**
  * Alters the server response with the suite nav data.
  */
  private _modifySuiteNavModel(suiteNavResponse: ISuiteNavModel): ISuiteNavModel {
    suiteNavResponse.NavBarData =
      this._suiteNavManagerConfiguration.modifySuiteNavData(suiteNavResponse.NavBarData);
    return suiteNavResponse;
  }

  /**
   * Gets the O365Shell object, once it's loaded. This will also load the shell CSS
   */
  private _getSuiteNavShell(suiteNavModel: ISuiteNavModel): Promise<IO365Shell> {
    if (!suiteNavModel) {
      this._createSuiteNavErrorLogEntry(
        'getO365Shell', 'SuiteNavModel is undefined');
    }

    // update the cache
    suiteNavModel.CacheToken = this._suiteNavManagerConfiguration.cacheToken;
    suiteNavModel.UserIdentifier = this._suiteNavManagerConfiguration.systemUserKey;
    SuiteNavManager._updateCache(suiteNavModel, this._suiteNavDataSource.suiteNavService);
    SPPageChrome.getInstance(this._serviceScope).changeSuiteNavHeight(suiteNavModel.NavBarData.Dimensions.Top);

    const element: HTMLLinkElement = document.createElement('link');
    element.rel = 'stylesheet';
    element.type = 'text/css';
    element.href = suiteNavModel.CssUrl;
    document.head.appendChild(element);

    const promise: Promise<IO365Shell> = new Promise<IO365Shell>(
      (complete?: (result: IO365Shell) => void) => {
        const loadShell: () => void = () => {
          const o365Shell: IO365Shell = new O365ShellWrapper();
          o365Shell.SetData(suiteNavModel.NavBarData);
          complete(o365Shell);
        };

        if (spModuleLoader && spModuleLoader.loadScript) {
          spModuleLoader.loadScript(suiteNavModel.JsUrl, '_o365su').then(loadShell);
        } else if (System && System.config && System.import) {
          System.config({
            map: {
              suiteNavJs: suiteNavModel.JsUrl
            },
            meta: {
              suiteNavJs: {
                format: 'global',
                exports: '_o365su'
              }
            }
          });

          System.import('suiteNavJs').then(loadShell);
        } else {
          throw new Error('No loader could be found, unable to load the shell.');
        }
      });
    return promise;
  }

  /**
   * Renders the Suite Nav.
   * @param o365Shell represents the Suite Nav shell wrapper.
   * @param suiteNavData repsents the Suite Nav data that will be used for rendering.
   */
  private _renderSuiteNav(o365Shell: IO365Shell): void {

    this._o365Shell = o365Shell;
    this._o365Shell.RenderAsync(
      this._suiteNavManagerConfiguration.o365ShellRenderSettings,
      () => {
        this._onRender(o365Shell);
      },
      () => {
        this._onPostRender();
      });
    this._o365Shell.OnHasMember('LoadShellPlus', () => {
      this._loadShellPlus(this._o365Shell);
    });
  }

  private _onRender(o365Shell: IO365Shell): void {
    if (this._suiteNavManagerConfiguration.hamburgerCallback) {
      if (o365Shell.Navigation.OnHamburgerButtonClick) {
        o365Shell.Navigation.OnHamburgerButtonClick(this._suiteNavManagerConfiguration.hamburgerCallback);
      }
    }
  }

  private _onPostRender(): void {
    if (this._suiteNavManagerConfiguration.suiteNavPostRenderCallback) {
      this._suiteNavManagerConfiguration.suiteNavPostRenderCallback();
    }
  }

  /**
    * LoadShellPlus will start downloading additional files required for notifications
    * and such. Once ShellPlus is ready, postRenderActions will be called.
    */
  private _loadShellPlus(o365Shell: IO365Shell): Promise<void> {
    return new Promise<void>(() => {
      o365Shell.LoadShellPlus();
    }).catch((errorMessage?: string) => {
      this._createSuiteNavErrorLogEntry(
        'loadShellPlus', errorMessage);
    });
  }

  private _isCacheValid(): boolean {
    // Cached data is valid (still usable for rendering) if:
    // - the CSS URL and JS URL are present (if we're trying to load the real suite nav)
    // - the data is for the same user
    // - the cache version is the same
    // - the site client tag is the same (if it's different, the theme has changed)
    const model: ISuiteNavModel = SuiteNavManager._cache.getValue<ISuiteNavModel>(SuiteNavManager.DATA_KEY);
    const oldVersion: number = SuiteNavManager._cache.getValue<number>(SuiteNavManager.VERSION_KEY);
    return (model && model.NavBarData &&
      ((model.JsUrl && model.CssUrl)) &&
      oldVersion === SuiteNavManager.CACHE_VERSION &&
      model.UserIdentifier &&
      model.UserIdentifier === this._suiteNavManagerConfiguration.systemUserKey &&
      model.CacheToken === this._suiteNavManagerConfiguration.cacheToken);
  }

  private _isCacheOutdated(): boolean {
    // "Outdated" means:
    // - it's more than 24 hours old
    // - it was not cached in the current browsing session (only applies if sessionStorage available)
    const cacheDate: number = Date.parse(SuiteNavManager._cache.getValue<string>(SuiteNavManager.DATE_KEY));
    const currentDate: Date = new Date();
    const cachedThisSession: DataStoreCachingType =
      SuiteNavManager._cache.getValue<DataStoreCachingType>(SuiteNavManager.SESSION_KEY, DataStoreCachingType.session);
    return (isNaN(cacheDate) ||
      currentDate.getTime() - cacheDate > 24 * 60 * 60 * 1000 ||
      (DataStore.hasStorageType(DataStoreCachingType.session) && !cachedThisSession));
  }

  private _createSPODataSource(): SPOSuiteNavDataSource {
    return new SPOSuiteNavDataSource(
      this._httpClient,
      this._suiteNavManagerConfiguration.currentUICultureName,
      this._suiteNavManagerConfiguration.webServerRelativeUrl);
  }

  private _createOnPremDataSource(): OnPremSuiteNavDataSource {
    return new OnPremSuiteNavDataSource(
      this._httpClient,
      this._suiteNavManagerConfiguration.currentUICultureName,
      this._suiteNavManagerConfiguration.webServerRelativeUrl);
  }

  /**
  * Create a log entry for logging
  * @param moduleName represents the module that the logger was called in
  * @param errorMessage represents the error string that occured
  */
  private _createSuiteNavErrorLogEntry(moduleName: string, errorMessage: string): void {
    _TraceLogger.logVerbose(SuiteNavManager._logSource, 'SuiteNavManager ' + moduleName + ' ' + errorMessage);
  }
}



