import { css } from 'office-ui-fabric-react/lib/utilities/css';
import { DisplayMode } from '@microsoft/sp-client-base';
import * as React from 'react';
import * as ReactDOM from 'react-dom';

import CanvasStore from '../canvasComponent/CanvasStore';
import { ICanvasContext } from '../canvasComponent/ICanvasComponent';
import { ICanvasToolboxHintProps } from './ICanvasToolboxHint';

import './CanvasToolboxHint.scss';

export default class CanvasToolboxHint extends React.Component<ICanvasToolboxHintProps, void> {
  public static contextTypes: React.ValidationMap<CanvasStore> = {
    store: React.PropTypes.object.isRequired
  };

  public context: ICanvasContext;

  private _domNode: HTMLElement;

  constructor(props: ICanvasToolboxHintProps, context: ICanvasContext) {
    super(props, context);
    this._handleClick = this._handleClick.bind(this);
  }

  public componentDidMount(): void {
    this._domNode = ReactDOM.findDOMNode(this) as HTMLElement;
    if (this.props.shouldFocus) {
      this.focus();
    }
  }

  public componentDidUpdate(): void {
    /**
     * Calling componentDidMount because we want the code to run
     * during each update as well.
     */
    this.componentDidMount();
  }

  public render(): React.ReactElement<ICanvasToolboxHintProps> {
    const { ariaLabel, isVisible }: ICanvasToolboxHintProps = this.props;

    const hintClassName: string = css(
      'CanvasToolboxHint',
      {
        'is-visible': isVisible
      }
    );

    return this.context.store.displayMode === DisplayMode.Edit && (
      <button
        aria-haspopup={true}
        aria-label={ariaLabel}
        className={hintClassName}
        onClick={this._handleClick}
      >
        <div className='CanvasToolboxHint-plusButton'>
          <i className='ms-Icon ms-Icon--Add'/>
        </div>
      </button>
    );
  }

  public focus(): void {
    /**
     * Setting a 0ms timeout to let the browser drawing finish before we set focus to an element.
     * Chrome's event queue lags more than IE's in this particular instance. Without this timeout
     * the focus will not be correct set in Chrome.
     */
    window.setTimeout(() => this._domNode.focus(), 0);
  }

  private _handleClick(): void {
    this.context.store.selectedControlId = undefined;
    this.context.store.openToolbox(this.props.row, this._domNode.offsetTop);
  }
}



