import {
  css,
  FocusZone
} from 'office-ui-fabric-react';
import * as React from 'react';

import { IToolbarButtonProps } from '../../canvas/canvas/ToolbarButton';
import { IToolbarSubmenuProps } from '../../canvas/canvas/ToolbarSubmenu';

import './SPRteFormattingBar.scss';

export interface ISPRteFormattingBarPosition {
  left: number;
  top: number;
  position: string;
}

export interface ISPRteFormattingBarProps {
  buttons?: React.ReactElement<IToolbarButtonProps | IToolbarSubmenuProps>[];
  position?: ISPRteFormattingBarPosition;
  onKeyDown?: (e: KeyboardEvent) => void;
}

export interface ISPRteFormattingBarState {
  isFaded: boolean;
}

/**
 * @class The formatting bar for the SPRte exposes text editing actions.
 */
export default class SPRteFormattingBar extends React.Component<ISPRteFormattingBarProps, ISPRteFormattingBarState> {
  private _focusZone: FocusZone;
  private _domNode: HTMLDivElement;

  constructor(props: ISPRteFormattingBarProps) {
    super(props);

    this.state = {
      isFaded: false
    };
  }

  public componentDidMount(): void {
    this._domNode.addEventListener('keydown', this.props.onKeyDown);
  }

  public componentWillReceiveProps(nextProps: ISPRteFormattingBarProps): void {
    if (this.props.position.top !== nextProps.position.top) {
      this.setState({
        isFaded: true
      });
    }
  }

  public componentDidUpdate(): void {
    if (this.state.isFaded) {
      window.setTimeout(() => {
        this.setState({
          isFaded: false
        });
      }, 300);
    }
  }

  /**
   * Renders the formatting bar in the position given in props
   * Formatting bar renders on top left side of the control by default
   * If there is a text selection the bar renders above the selection
   */
  public render(): React.ReactElement<ISPRteFormattingBarProps> {
    const style: React.CSSProperties = this.props.position;
    const className: string = css(
      'SPRteFormattingBar',
      'rteFlyout-visible',
      'stack-panel-class',
      'ms-u-slideUpIn10',
      {
        'faded':  this.state.isFaded
      }
    );

    return (
      <div
        role='toolbar'
        aria-label='Formatting'
        className={className}
        ref={(ref) => this._domNode = ref}
        style={style}
      >
        <FocusZone ref={(ref) => this._focusZone = ref} isCircularNavigation={true}>
          {this.props.buttons}
        </FocusZone>
      </div>
    );
  }

  public focus(): void {
    this._focusZone.focus();
  }
}



