import * as React from 'react';
import { css } from 'office-ui-fabric-react/lib/utilities/css';
import { DisplayMode } from '@microsoft/sp-client-base';
import { StringHelper } from '@ms/odsp-utilities-bundle';

import CanvasControlType from '../../../canvas/canvas/CanvasControlType';
import { ICanvasContext } from '../canvasComponent/ICanvasComponent';
import { ICanvasZoneProps } from './ICanvasZone';
import {
  isEscape,
  isKey,
  KeyCodes
} from '../../a11y/CanvasA11y';
import SPRte from '../../../sp-rte/rte/SPRte';
import WebPartZone, { IWebPartZoneProps } from '../../../canvas/sampleCanvasControls/WebPartZone';
import {  } from '../../../../../common/webParts/IWebPartHost';

import { PropertyPaneState } from '../../../../../common/webParts/PropertyPaneDefinitions';
import './CanvasZone.scss';
import Strings from '../../loc/CanvasStrings.resx';

export default class CanvasZone extends React.Component<ICanvasZoneProps, void> {
  public static contextTypes: React.ValidationMap<CanvasZone> = {
    store: React.PropTypes.object.isRequired
  };

  /**
   * @ readOnly
   */
  private static _VERTICAL_MARGIN: number = 22;

  public context: ICanvasContext;

  private _childControl: SPRte | WebPartZone;
  private _childControlElement: HTMLElement;
  private _domElement: HTMLElement;
  private _toolbarElement: HTMLElement;
  private _trapDiv: HTMLElement;

  constructor(props: ICanvasZoneProps, context: ICanvasContext) {
    super(props, context);

    this._handleClickCapture = this._handleClickCapture.bind(this);
    this._handleKeydownCapture = this._handleKeydownCapture.bind(this);
    this._handleMouseOut = this._handleMouseOut.bind(this);
    this._handleMouseOver = this._handleMouseOver.bind(this);
    this._handleToolbarKeydown = this._handleToolbarKeydown.bind(this);
    this._handleTrapFocus = this._handleTrapFocus.bind(this);
    this._handleControlFocus = this._handleControlFocus.bind(this);
    this._selectControl = this._selectControl.bind(this);
  }

  public shouldComponentUpdate(nextProps: ICanvasZoneProps): boolean {
    const { displayMode, isSelected, row }: ICanvasZoneProps = this.props;
    return (
      displayMode !== nextProps.displayMode ||
      isSelected !== nextProps.isSelected ||
      row !== nextProps.row
    );
  }

  public componentDidMount(): void {
    if (this.context.store.selectedControlId === this.props.id) {
      // 500ms animation duration
      this.context.store.scrollIntoView('partial', this._domElement, 500, CanvasZone._VERTICAL_MARGIN * 2);
      this.context.store.a11yManager.navigateTo(this._domElement);
    }

    this._childControl = this.context.store.getControl(this.props.id);

    if (this.props.displayMode === DisplayMode.Edit) {
      this._toolbarElement.addEventListener('keydown', this._handleToolbarKeydown);
      this._childControlElement.addEventListener('keydown', this._handleKeydownCapture, true);
    }
  }

  public componentDidUpdate(prevProps: ICanvasZoneProps): void {
    const { displayMode }: ICanvasZoneProps = this.props;
    if (prevProps.displayMode !== displayMode) {
      if (displayMode === DisplayMode.Edit) {
        this._toolbarElement.addEventListener('keydown', this._handleToolbarKeydown);
        this._childControlElement.addEventListener('keydown', this._handleKeydownCapture, true);
      } else if (displayMode === DisplayMode.Read) {
        this._toolbarElement.removeEventListener('keydown', this._handleToolbarKeydown);
        this._childControlElement.removeEventListener('keydown', this._handleKeydownCapture, true);
      }
    }
  }

  public render(): React.ReactElement<ICanvasZoneProps> {
    const { children, displayMode, isSelected, id }: ICanvasZoneProps = this.props;
    const { controlType }: IWebPartZoneProps = children[0].props;
    const isReadMode: boolean = displayMode === DisplayMode.Read;

    let accessibleLabel: string = '';
    const ariaPrimaryLabelById: string = `cswpAccessibleLabel_${id}`;
    // Keep ID in sync with BaseClientSideWebpart._renderWithAccessibileTitle,
    // where each webpart can provide contextual info based on its properties:
    // ie. "image of a house" instead of just "image"
    const ariaContextualLabelId: string = `cswpAccessibleLabelContextual_${id}`;
    // Have to provide multiple IDs because IE doesn't care for aria-describedBy on DIVs
    let ariaLabelledBy: string;

    if (controlType === CanvasControlType.WebPartZone) {
      // Want additional listed first since it will provide most contextual info
      ariaLabelledBy = ariaContextualLabelId;
    } else if (controlType === CanvasControlType.RTE) {
      accessibleLabel = StringHelper.format(Strings.CanvasZoneAriaWebpartName || '',
        Strings.TextWebPartDisplayName || '') || '';
      // No contextual info available for text
      ariaLabelledBy = ariaPrimaryLabelById;
    }

    if (displayMode === DisplayMode.Edit) {
      accessibleLabel = StringHelper.format(Strings.AriaWebPartEnterTemplate || '', accessibleLabel) || '';
      if (controlType === CanvasControlType.WebPartZone) {
        ariaLabelledBy += ` ${ariaPrimaryLabelById}`;
      }
    }

    const zoneClassName: string = css(
      'CanvasZone',
      {
        'CanvasZone--edit': !isReadMode,
        'CanvasZone--selected': isSelected
      }
    );

    return (
      /**
       * Passing undefined once you set the tab index does not remove the tab index in React 14.
       * This is fixed in React 15. https://github.com/facebook/react/issues/2528
       */
      <div
        aria-labelledby={ariaLabelledBy}
        className={zoneClassName}
        ref={(ref) => this._domElement = ref}
        onMouseOut={this._handleMouseOut}
        onMouseOver={this._handleMouseOver}
        tabIndex={isReadMode ? -1 : 0}
      >
        <div>
          <div
            className='CanvasZone-control'
            isHidden={isReadMode}
            onClickCapture={isReadMode ? undefined : this._handleClickCapture}
            onFocus={isReadMode ? undefined : this._handleControlFocus}
            ref={(ref) => this._childControlElement = ref}
          >
            { children[0] }
          </div>
          <div
            isHidden={isReadMode}
            ref={(ref) => this._toolbarElement = ref}
          >
            { children[1] }
          </div>
          <div id={ariaPrimaryLabelById}
              className='CanvasZone-screenReaderOnly'>
              { accessibleLabel }
          </div>
        </div>
      </div>
    );
  }

  private _handleControlFocus(e: React.FocusEvent): void {
    this._selectControl();
  }

  private _handleClickCapture(e: React.MouseEvent): void {
    this._selectControl();
  }

  private _handleKeydownCapture(evt: KeyboardEvent): void {
    if (this._childControl && this._childControl.props.controlType === CanvasControlType.WebPartZone) {
      if (isEscape(evt)) {
        this.context.store.a11yManager.navigateTo(this._domElement);
        evt.stopPropagation();
        evt.preventDefault();
      } else if (isKey(KeyCodes.F10, evt, true)) {
        this.context.store.a11yManager.navigateTo(this._toolbarElement);
        this.context.store.a11yManager.ariaAlert(Strings.ToolbarNavigationArrowKeys);
        evt.stopPropagation();
        evt.preventDefault();
      } else if (isKey(KeyCodes.P, evt, true)) {
        this.context.store.webPartManager.configureStart(this._childControl.props.id, PropertyPaneState.Open);

        evt.stopPropagation();
        evt.preventDefault();
      } else {
        this.context.store.a11yManager.skipEvent(evt);
      }
    }
  }

  private _handleToolbarKeydown(evt: KeyboardEvent): void {
    if (this._childControl && this._childControl.props.controlType === CanvasControlType.RTE) {
      // Only for RTE: pressing escape on toolbar takes the focus inside the editor
      if (isEscape(evt)) {
        this._childControl.focus();
        evt.stopPropagation();
        evt.preventDefault();
      }
    }
  }

  private _handleMouseOut(e: React.MouseEvent): void {
    this.context.store.hoveredControlId = undefined;
  }

  private _handleMouseOver(e: React.MouseEvent): void {
    this.context.store.hoveredControlId = this.props.id;
  }

  private _handleTrapFocus(e: React.FocusEvent): void {
    if (e.target === this._trapDiv) {
      this._childControl.focus();
    }
  }

  private _selectControl(): void {
    if (this.context.store.selectedControlId !== this.props.id) {
      this.context.store.selectedControlId = this.props.id;
    }
  }

}


