/**
 * WebPart PropertyPane configuration events
 */
export enum WebPartConfigurationEvent {
  ConfigurationComplete = 1,
  OpenComplete = 2,
  CloseComplete = 3,
  ApplyClicked = 4,
  LostFocus = 5
}

/**
 * Describes the PropertyPane state.
 */
export enum PropertyPaneState {

  // PropertyPane's open state.
  Open = 1,

  // PropertyPane's close state.
  Close = 2,

  // PropertyPane's Toggle state.
  Toggle = 3,

  // PropertyPane's default state.
  // if it's Open it remains open with the newly selected web part's context.
  // If it's closed it stays closed.
  Default
}

/**
 * Configuration event callback
 */
export interface IWebPartConfigurationEventCallback {
  /* tslint:disable:no-any */
  // @todo (SPPPLAT VSO#216544): change the any to IWebPartData
  (event: WebPartConfigurationEvent, data?: any): void;
  /* tslint:enable:no-any */
}



