import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { css } from 'office-ui-fabric-react/lib/utilities/css';
import { DisplayMode } from '@microsoft/sp-client-base';

import CanvasControlFactory from '../canvasControl/CanvasControlFactory';
import CanvasStore from './CanvasStore';
import CanvasZone from '../canvasZone/CanvasZone';
import { CanvasA11yManager } from '../../a11y/CanvasA11y';
import CanvasToolboxHint from '../canvasToolboxHint/CanvasToolboxHint';
import ClientSideWebPartManager from '../../../../../clientSideWebParts/core/ClientSideWebPartManager';
import ConfirmationDialog from '../confirmationDialog/ConfirmationDialog';
import { ICanvasContext, ICanvasComponentProps } from './ICanvasComponent';
import { ICanvasZoneProps } from '../canvasZone/ICanvasZone';
import { ISerializedCanvasControl } from '../canvasControl/ISerializedCanvasControl';
import { IToolboxProps } from '../../toolbox/toolbox/IToolbox';
import Toolbar from '../../toolbar/Toolbar';
import Toolbox from '../../toolbox/toolbox/Toolbox';

import Strings from '../../loc/CanvasStrings.resx';

export default class CanvasComponent extends React.Component<ICanvasComponentProps, {}> {
  public static childContextTypes: React.ValidationMap<ClientSideWebPartManager> = {
    store: React.PropTypes.object.isRequired
  };

  constructor(props: ICanvasComponentProps, context: ICanvasContext) {
    super(props, context);
  }

  public getChildContext(): ICanvasContext {
    return {
      store: this.props.store
    };
  }

  public render(): React.ReactElement<ICanvasComponentProps> {
    const store: CanvasStore = this.props.store;

    const controlsProps: ISerializedCanvasControl[] = store.controlsProps;
    const deletedRow: number = store.deletedRow;
    const displayMode: DisplayMode = store.displayMode;
    const toolboxProps: IToolboxProps = store.toolboxProps;

    const rows: React.ReactElement<ICanvasZoneProps>[] = [];
    let row: number = 0;
    if (controlsProps) {
      for (const control of controlsProps) {
        const isSelected: boolean = store.selectedControlId === control.id;

        if (isSelected && row) {
          // Replace the last hint pushed with a hint that is visible
          rows.pop();
          rows.push(
            <CanvasToolboxHint
              ariaLabel={Strings.ToolboxHintTitle}
              isVisible={true}
              key={row}
              row={row}
            />
          );
        }

        rows.push(
          <CanvasZone
            displayMode={displayMode}
            id={control.id}
            isSelected={isSelected}
            key={control.id}
            row={row}
          >
            { CanvasControlFactory.CreateCanvasControl(control, store) }
            <Toolbar
              controlType={control.controlType}
              displayMode={displayMode}
              id={control.id}
              row={row}
              onConfigureButtonClick={store.handleConfigureButtonClicked}
              onDeleteButtonClick={store.handleDeleteButtonClicked}
            />
          </CanvasZone>
        );

        ++row;

        rows.push(
          <CanvasToolboxHint
            ariaLabel={Strings.ToolboxHintTitle}
            isVisible={toolboxProps.row === row}
            shouldFocus={deletedRow === row}
            key={row}
            row={row}
          />
        );
      }
    }

    /**
     * Only set focus on a hint during the render after control is deleted.
     */
    store.deletedRow = undefined;

    const className: string = css(
      'Canvas',
      {
        'Canvas--edit': displayMode === DisplayMode.Edit
      }
    );

    return (
      <div
        className={className}
        role={displayMode === DisplayMode.Edit ? 'application' : undefined}
      >
        <CanvasToolboxHint
          ariaLabel={Strings.ToolboxHintTitle}
          shouldFocus={deletedRow === 0}
          isVisible={rows[0] && rows[0].props.isSelected || toolboxProps.row === 0}
          row={0}
        />
        { rows }
        <Toolbox {...toolboxProps}/>
        <ConfirmationDialog
          isOpen={store.isDeleteDialogOpen}
          message={Strings.DeleteConfirmationDialogMessage}
          onConfirmation={store.handleDeleteDialogConfirmation}
          onDismiss={store.handleDeleteDialogDismiss}
          title={Strings.DeleteConfirmationDialogTitle}
        />
      </div>
    );
  }

  public componentDidMount(): void {
    const canvasElement: HTMLElement = ReactDOM.findDOMNode(this) as HTMLElement;
    this.props.store.canvasElement = canvasElement;
    this.props.store.a11yManager = new CanvasA11yManager(canvasElement);

    // Find and set scrollable parent
    let canvasContainer: HTMLElement = canvasElement;
    let hasScrollbar: boolean = false;
    do {
      canvasContainer = canvasContainer.parentElement;
      const overflowY: string = window.getComputedStyle(canvasContainer)['overflow-y'];
      hasScrollbar = (overflowY === 'scroll' || overflowY === 'auto');
    } while (!hasScrollbar && canvasContainer.tagName !== 'BODY');
    this.props.store.scrollableParent = canvasContainer;
  }
}



