import { IPropertyPaneSliderProps } from './IPropertyPaneSlider';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneField/IPropertyPaneField';

/**
 * Helper method to create a Slider on the PropertyPane.
 * @param targetProperty - Target property the slider is associated to.
 * @param properties - Strongly typed Slider properties.
 */
export function PropertyPaneSlider(
  targetProperty: string,
  properties: IPropertyPaneSliderProps): IPropertyPaneField<IPropertyPaneSliderProps> {

  return {
    type: IPropertyPaneFieldType.Slider,
    targetProperty: targetProperty,
    properties: properties
  };
}



