// TODO (Bug 223178) Move Theming engine from odsp-utilities to sp-client\platform\sp-client-shared
import { Promise as ODSPPromise } from '@ms/odsp-utilities-bundle';
import { FabricTheming, RgbaColor } from '@ms/odsp-utilities-bundle';
import { ThemeProvider, WebTheme, Uri } from '@ms/odsp-utilities-bundle';
import { IThemeData, IThemeDataRaw } from '@ms/odsp-utilities-bundle';

import {
  HttpClient,
  ServiceScope,
  httpClientServiceKey,
  _QosMonitor
} from '@microsoft/sp-client-base';

import PageContext, { appPageContextServiceKey } from '../../framework/pageContext/AppPageContext';
import SPWebTemplateIds from './SPWebTemplateIds';

import { loadTheme } from 'load-themed-styles';

// This is a map of tokenName 'theme:themePrimary' to color value '#0078d7'
interface IColorPalette {
  [key: string]: string;
}

/**
 * The class controls applying a theme to the current Page.
 * The theme is a web level setting, if a theme is applied this class with call loadtheme
 * which will update the styles that are registered with load-themed-styles.
 *
 * The loadData method is called by the ThemeProvider class when it needs to get the IThemeData.
 * Once it has theme data it will cache it in local storage using the themedCssFolderUrl as the cache key.
 */
export default class SPThemeProvider {

  private _pageContext: PageContext;
  private _httpClient: HttpClient;
  private _themeOverride: string;
  private _themeProvider: ThemeProvider;

  public constructor(serviceScope: ServiceScope) {

    serviceScope.whenFinished(() => {
      this._pageContext = serviceScope.consume(appPageContextServiceKey);
      this._httpClient = serviceScope.consume(httpClientServiceKey);
      this._themeProvider = new ThemeProvider(this.loadData.bind(this));

      // Check for a theme override from the URL (as in the theme preview page).
      try {
        const contextUri: Uri = new Uri(window.location.href);
        this._themeOverride = contextUri.getQueryParameter('ThemeOverride');
      } catch (ignoredUrlException) {
        // Just in case location.href throws for some reason.
      }
    });
  }

  public loadThemedStyles(): void {
    const cacheToken: string = this.getCacheToken();
    this._themeProvider.loadThemeTokenMap(cacheToken)
      .then((palette: IColorPalette) => {
        loadTheme(palette);
      });
  }

  public loadData(): ODSPPromise<IThemeData> {
    const tagName: string = 'SPThemeProvider.loadData';
    const qosMonitor: _QosMonitor = new _QosMonitor(tagName);

    if (this._pageContext.core.webTemplate) {
      if (this._pageContext.core.webTemplate === SPWebTemplateIds.teamSite ||
          this._pageContext.core.webTemplate === SPWebTemplateIds.groupSite) {
        // check if the site has a theme applied
        if (this._pageContext.core.themedCssFolderUrl) {
          // Fetch Theme Data from SharePoint
          const getThemeDataUrl: string = WebTheme.makeWebThemeRestUrl(this._pageContext.core.siteServerRelativeUrl,
                                                                       this._pageContext.core.currentUICultureName,
                                                                       this._themeOverride);
          return new ODSPPromise((resolve, reject) => {
            this._httpClient.get(getThemeDataUrl)
            .then((response: Response) => {
              if (response.status === 200) {
                return response.json()
                  /* tslint:disable:no-any */
                  .then((json: any) => {
                  /* tslint:enable:no-any */
                    const themeDataRaw: IThemeDataRaw = JSON.parse(json.value) as IThemeDataRaw;
                    if (themeDataRaw) {
                      const themeData: IThemeData = WebTheme.processRawThemeData(themeDataRaw);
                      themeData.cacheToken = this._pageContext.core.themedCssFolderUrl;
                      qosMonitor.writeSuccess();
                      resolve(themeData);
                    } else {
                      // ThemeProvider will handle undefined themeData as an error
                      qosMonitor.writeUnexpectedFailure();
                      resolve(undefined);
                    }
                });
              } else {
                // ThemeProvider will handle undefined themeData as an error
                qosMonitor.writeUnexpectedFailure(tagName, new Error(response.statusText));
                resolve(undefined);
              }
            });
          });
        } else if (this._pageContext.core.groupColor) {
          // Generate a Fabric theme from the site color
          const htmlThemeColor: string = this._pageContext.core.groupColor;
          const rgbaThemeColor: RgbaColor = RgbaColor.fromHtmlColor(htmlThemeColor);
          if (rgbaThemeColor) {
              const fabTheme: IThemeData = {
                  backgroundImageUri: undefined,
                  cacheToken: this.getCacheToken(),
                  isDefault: false,
                  isInverted: false,
                  palette: FabricTheming.generateFabricColors(rgbaThemeColor),
                  version: '1'
              };
              return ODSPPromise.wrap(fabTheme);
          } else {
              // Return no theme to indicate default theme values should be used.
              return ODSPPromise.wrap();
          }
        } else {
          // Use Default SharePoint Color Palette
          qosMonitor.writeSuccess();
          return ODSPPromise.wrap(this.createDefaultTheme());
        }
      } else {
        // Unknown Web Template, Use Default
        qosMonitor.writeExpectedFailure(`Unsuppted WebTemplate: ${this._pageContext.core.webTemplate}`);
        return ODSPPromise.wrap(this.createDefaultTheme());
      }
    } else {
      qosMonitor.writeUnexpectedFailure(`pageContext.core.webTemplate is undefined`);
      return ODSPPromise.wrap(this.createDefaultTheme());
    }
  }

  private createDefaultTheme(): IThemeData {
    const themeData: IThemeData = {
      backgroundImageUri: undefined,
      cacheToken: undefined,
      isDefault: undefined,
      isInverted: false,
      palette: undefined,
      version: '1.0'
    };

    return themeData;
  }

  private getCacheToken(): string {
    const webTemplate: string = String(this._pageContext.core.webTemplate);
    const groupColor: string =
      webTemplate === SPWebTemplateIds.teamSite ||
      webTemplate === SPWebTemplateIds.groupSite ? this._pageContext.core.groupColor : undefined;
    const groupColorToken: string = groupColor && (groupColor + ';web#' + webTemplate);
    return this._themeOverride || this._pageContext.core.themedCssFolderUrl || groupColorToken;
  }
}



