import {
  JsonUtilities,
  ServiceScope,
  PageContext,
  pageContextServiceKey
} from '@microsoft/sp-client-base';
import IWebPartData from '../IWebPartData';
import IWebPartHost from '../../../../local/common/webParts/IWebPartHost';
import { WebPartConfigurationEvent } from '../../../../local/common/webParts/PropertyPaneDefinitions';

/**
 * This class represents the host for client side web parts on classic SharePoint pages.
 * @see IWebPartHost for more details on the role of a web part host.
 */
export class ClassicPageHost implements IWebPartHost {
  private static _instance: ClassicPageHost;
  private _serviceScope: ServiceScope;

  public static getInstance(): ClassicPageHost {
    if (ClassicPageHost._instance === undefined) {
      ClassicPageHost._instance = new ClassicPageHost();
    }
    return ClassicPageHost._instance;
  }

  /**
   * constructor
   */
  constructor() {
    this.setDirty = this.setDirty.bind(this);
    this.webPartConfigurationEventCallback = this.webPartConfigurationEventCallback.bind(this);
  }

  /**
   * @see IWebPartHost
   */
  public get serviceScope(): ServiceScope {
    this._ensureInitialized();
    return this._serviceScope;
  }

  /* tslint:disable:no-any */
  public setDirty(instanceId: string, data?: any): void {
  /* tslint:enable:no-any */
    this._setData(data);
  }

  /**
   * @see IWebPartHost.ts
   */
  public webPartConfigurationEventCallback(event: WebPartConfigurationEvent, data?: IWebPartData): void {
    if (event === WebPartConfigurationEvent.ConfigurationComplete) {
      this._setData(data);
    }
  }

  /* tslint:disable:no-any */
  private _setData(data?: any): void {
  /* tslint:enable:no-any */
    if (data) {
      const dataField: Element = document.querySelector('#ClientSideToolPartData');
      const idField: Element = document.querySelector('#ClientSideToolPartId');
      const webPartBeingConfiguredId: string = idField.getAttribute('value');
      if (dataField && webPartBeingConfiguredId === data.id) {
        dataField.setAttribute('value', JsonUtilities.encode(JSON.stringify(data)));
      }
    }
  }

  private _ensureInitialized(): void {
    const serviceScope: ServiceScope = ServiceScope.startNewRoot();

    // This bootstrapping code should be reconciled with how applications are loaded by the shell.

    // Task 228547 should remove this code
    const pageContext: PageContext = serviceScope.createDefaultAndProvide(pageContextServiceKey);
    const pageContextInfoName: string = '_spPageContextInfo';
    pageContext.initialize({
      cultureInfo: {
        currentCultureName: window[pageContextInfoName].currentCultureName,
        currentUICultureName: window[pageContextInfoName].currentUICultureName
      },
      web: {
        absoluteUrl: window[pageContextInfoName].webAbsoluteUrl,
        id: window[pageContextInfoName].webId,
        serverRelativeUrl: window[pageContextInfoName].webServerRelativeUrl,
        title: window[pageContextInfoName].webTitle
      },
      site: {
        id: window[pageContextInfoName].siteId
      },
      user: {
        displayName: window[pageContextInfoName].userDisplayName,
        loginName: window[pageContextInfoName].userLoginName
      }
    });

    serviceScope.finish();
    this._serviceScope = serviceScope;
  }
}

/* tslint:disable:export-name */
export default ClassicPageHost;
/* tslint:enable:export-name */



